import { Injectable } from '@angular/core';
import { SysConfigs } from '../constants/sysConfigs';
import { BehaviorSubject, Subject, Observable } from 'rxjs';
import { ParkingDictionary } from '../constants/parkingDictionary';
import { DeviceService, IDInfo } from '../device/device.service';
import { CommonService } from '../common.service';
import { Http, Response, Headers, URLSearchParams, RequestOptions } from '@angular/http';

declare let $: any;
declare let SockJS: any;
declare let Stomp: any;
@Injectable()
export class CloudService {

    constructor(private sysConfigs: SysConfigs, private device: DeviceService, private common: CommonService, private http: Http) {
        Observable.interval(5000).subscribe(() => {
            if (this.state == false) {
                this.connect();
            }
        });
    }
    /**定义websocket对象 */
    stompClient;
    /**
    * orderKey:订阅key
    * connectFn：连接后执行函数
    * subscribeFn:订阅后执行函数
    * sysDomain:订阅域名
    **/
    state: boolean = false;

    connect() {
        // this.stompClient = Stomp.over(new SockJS("http://192.168.1.168/websocket"));
        this.stompClient = Stomp.over(new SockJS(this.sysConfigs.SERVER_SOCKET));        
        this.stompClient.connect({}, (frame) => {
            this.state = true;
            /**ID卡充值 */
            this.stompClient.subscribe(`/cardRecharge/idCardIssued/${JSON.parse(localStorage.baseInfo).macAddress}`, (resp) => {
                console.log(resp);

                let body = JSON.parse(resp.body);
                body.deviceNo.split(",").forEach(element => {
                    let opt: IDInfo = {
                        hw_id: element,
                        card_id: body.cardNum,
                        card_type: "0",
                        op_type: 'set',
                        time_to: this.device.timeNumberToString(body.endDate),
                    }

                    this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op == "ret-publish-id-card-info").subscribe((data) => {
                        let parameter = {
                            cardNum: data.card_id,
                            macAddress: JSON.parse(localStorage.baseInfo).macAddress
                        }
                        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
                        this.http.post(this.sysConfigs.SERVER_PARKING + 'idCardIssued/cancel', parameter, headers).subscribe((resp)=>{});
                    });
                    this.device.sendIDCardToDevice(opt);
                });
            });

            /**IC/ID挂失 */
            this.stompClient.subscribe(`/cardLoss/lossCard/${JSON.parse(localStorage.baseInfo).macAddress}`, (resp) => {
                console.log(resp);

                let body = JSON.parse(resp.body);
                body.deviceNo.split(",").forEach(element => {
                    let opt: IDInfo = {
                        hw_id: element,
                        card_id: body.cardNum,
                        card_type: this.toNumberType(body.cardType),
                        op_type: 'set',
                        time_to: "2013-09-01 09:00:00",
                    }
                    this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op == "ret-publish-id-card-info").subscribe((data) => {
                        let parameter = {
                            cardNum: data.card_id,
                            macAddress: JSON.parse(localStorage.baseInfo).macAddress
                        }
                        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
                        this.http.post(this.sysConfigs.SERVER_PARKING + 'cardLoss/cancel', parameter, headers).subscribe((resp)=>{});
                    });
                    this.device.sendIDCardToDevice(opt);
                });
            });

            /**IC/ID卡停用 */
            this.stompClient.subscribe(`/cardDisable/disableCard/${JSON.parse(localStorage.baseInfo).macAddress}`, (resp) => {
                console.log(resp);

                let body = JSON.parse(resp.body);
                body.deviceNo.split(",").forEach(element => {
                    let opt: IDInfo = {
                        hw_id: element,
                        card_id: body.cardNum,
                        card_type: this.toNumberType(body.cardType),
                        op_type: 'set',
                        time_to: "2013-09-01 09:00:00",
                    }
                    this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op == "ret-publish-id-card-info").subscribe((data) => {
                        let parameter = {
                            cardNum: data.card_id,
                            macAddress: JSON.parse(localStorage.baseInfo).macAddress
                        }
                        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
                        this.http.post(this.sysConfigs.SERVER_PARKING + 'cardDisable/cancel', parameter, headers).subscribe((resp)=>{});
                    });
                    this.device.sendIDCardToDevice(opt);
                });
            });

            /**IC/ID卡解挂 */
            this.stompClient.subscribe(`/cardResetLoss/resetCardLoss/${JSON.parse(localStorage.baseInfo).macAddress}`, (resp) => {
                console.info(resp);
                let body = JSON.parse(resp.body);
                body.deviceNo.split(",").forEach(element => {
                    let opt: IDInfo = {
                        hw_id: element,
                        card_id: body.cardNum,
                        card_type: this.toNumberType(body.cardType),
                        op_type: 'set',
                        time_to: this.device.timeNumberToString(body.endDate),
                    }
                    this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op == "ret-publish-id-card-info").subscribe((data) => {
                        let parameter = {
                            cardNum: data.card_id,
                            macAddress: JSON.parse(localStorage.baseInfo).macAddress
                        }
                        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
                        this.http.post(this.sysConfigs.SERVER_PARKING + 'cardResetLoss/cancel', parameter, headers).subscribe((resp)=>{});
                    });
                    this.device.sendIDCardToDevice(opt);
                });
            });
        });
    }

    disconnect() {
        if (this.stompClient != null) {
            this.state = false;
            this.stompClient.disconnect();
        }
        console.info('Disconnected');
    }


    cancelIDcardIssued() {
        this.stompClient.send("/idCardIssued/cancel", {}, {
            cardNum: "004873C0",
            macAddress: "f4:8e:38:b6:a9:08"
        });
    }

    toNumberType(type) {
        if (type == "parking_id") {
            return "0";
        }
        else {
            return "2";
        }
    }
}


