import { Injectable } from '@angular/core';
import { SysConfigs } from '../constants/sysConfigs';
import { Http, Response, Headers, URLSearchParams, RequestOptions } from '@angular/http';
import { BehaviorSubject, Subject } from 'rxjs'

@Injectable()
export class RoadService {
    roadList$ = new Subject<any>();
    parkOptList$ = new Subject<any>();    
    constructor(private http: Http, private sysConfigs: SysConfigs) { }
    /**获得机号设置列表 */
    getRoadList(page, map = { /**deviceNo: "4"*/ }) {
        let _searchParams = {
            page: page,
            pageSize: 10,
            map: map
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'parksettingRoadway/list', _searchParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    if (resp.json().object.collection) {
                        let obj = {
                            collection: resp.json().object.collection,
                            totalSize: resp.json().object.totalSize,
                            totalPage: Math.ceil(resp.json().object.totalSize / resp.json().object.pageSize)
                        }
                        this.roadList$.next(obj);
                    }
                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });

    }
    /**获得车场设置 */
    getParkingOptList() {
        // let _searchParams = {
        //     page: page,
        //     pageSize: 10,
        //     map: map
        // }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.get(this.sysConfigs.SERVER_PARKING + `parksetting/showParksetting?parkId=${JSON.parse(localStorage.adminUser).parkId}`, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    if (resp.json().object) {
                        this.parkOptList$.next(resp.json().object);
                    }
                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });

    }
    /*得到新增数据*/
    getParksettingAddData(object:any){
       console.log("服务数据是"+JSON.stringify(object));
       object.checkCard=this.boolToString(object.checkCard);
       object.getCard=this.boolToString(object.getCard);
       object.Monitor=this.boolToString(object.Monitor);
       let _searchParams={
           "laneMarking":object.addRoadNumber,
           "entryMarking":object.entranceAndExit_Identification,
           "entryName":object.entranceAndExit,
           "controlNum":object.addcontroller,
           "openedNum":"1",
           "openedType":object.openWay,
           "videoCardNum":"1",
           "garageMarking":"1",
           "inspectionMarking":object.checkCard,
           "isCardMachine":object.getCard,
           "isMonitoring":object.Monitor,
        // "isCardMachine":"0",
        // "isMonitoring":"1",
        // "inspectionMarking":"1",
           "computerName":"",
           "macAdress":"Mac"

       };
       let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
       this.http.post(this.sysConfigs.SERVER_PARKING + 'parksettingRoadway/add', _searchParams, headers).subscribe((resp: Response) => {
        console.log("数据是"+resp.json().object);
       });
    }

        /**布尔值转化为字符型 */
        boolToString(bool) {
            if (bool == "true" || bool == "1") {
                return "1"
            }
            else {
                return "0"
            }
        }
}


