import { Injectable } from '@angular/core';
import { SysConfigs } from './constants/sysConfigs';
import { ModalService } from './modal/modal.service';
import { Http, Response, Headers, URLSearchParams, RequestOptions } from '@angular/http';
import { BehaviorSubject, Subject } from 'rxjs'
import { RenderService } from './render/render.service';
import { Router } from '@angular/router';
@Injectable()
export class UserService {
    constructor(private http: Http, private sysConfigs: SysConfigs, private render: RenderService, private modal: ModalService, private router: Router) {
        /**当登录成功后重新请求数据 */
        this.render.on('login-reply', (event, arg) => {
            localStorage.setItem('token', arg.object.token);
            let adminUser = {
                adminUserId: arg.object.adminUserId ? arg.object.adminUserId : "",
                parkId: arg.object.activeParkId ? arg.object.activeParkId : "",
                createId: arg.object.id ? arg.object.id : "",
                createName: arg.object.userAccount ? arg.object.userAccount : "",
                // createTime: arg.object.createTime?arg.object.createTime:"",
                updateId: arg.object.id ? arg.object.id : "",
                updateName: arg.object.userAccount ? arg.object.userAccount : "",
                // updateTime: arg.object.createTime?arg.object.createTime:"",
            }
            localStorage.setItem('adminUser', JSON.stringify(adminUser));
            this.userInfo$.next(arg.object.userAccount);
            this.permission$.next(['lessor']);
            this.isConnect$.next(true);
        });

        /**离线登录成功 */
        this.render.on('login-reply-outline', (event, arg) => {
            localStorage.setItem('token', arg.token);
            this.userInfo$.next(arg.userAccount);
            this.permission$.next(['lessor']);
            alert("连接超时，现在是离线登录状态，请告之管理员");
            this.isConnect$.next(false);
        });
    }
    /**定义用户信息流 */
    userInfo$ = new BehaviorSubject("");
    /**验证密码正确流 */
    isTruePassword$ = new Subject<boolean>();
    /**是否已经成功修改了密码流 */
    isSuccessChange$ = new Subject<boolean>();
    /**权限流 */
    permission$ = new Subject<Array<string>>();
    /**用户登录状态流，true为在线，false为离线 */
    isConnect$ = new Subject<boolean>();
    /**用户电脑信息 */
    baseInfo$ = new Subject<any>();
    /**是否登录 */
    isLogin = false;
    /**登录后重定向的路由 */
    redirectUrl: string;

    /**登录方法 */
    login(account, password) {
        let _userParams = new URLSearchParams();
        _userParams.set("userAccount", account);
        _userParams.set("userPassword", password);
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_FORM }) });
        this.http.post(this.sysConfigs.SERVER_PORTAL + 'user/login4DesktopApp', _userParams, headers).subscribe((resp: Response) => {
            // this.http.post(this.SERVER_PORTAL + 'user/login4DesktopApp', _userParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    //token
                    localStorage.setItem('token', resp.json().object.token);
                    //adminUser Info
                    let adminUser = {
                        adminUserId: resp.json().object.adminUserId ? resp.json().object.adminUserId : "",
                        parkId: resp.json().object.activeParkId ? resp.json().object.activeParkId : "",
                        createId: resp.json().object.id ? resp.json().object.id : "",
                        createName: resp.json().object.userAccount ? resp.json().object.userAccount : "",
                        // createTime: resp.json().object.createTime?resp.json().object.createTime:"",
                        updateId: resp.json().object.id ? resp.json().object.id : "",
                        updateName: resp.json().object.userAccount ? resp.json().object.userAccount : "",
                        // updateTime: resp.json().object.createTime?resp.json().object.createTime:"",
                    }
                    localStorage.setItem('adminUser', JSON.stringify(adminUser));
                    this.userInfo$.next(resp.json().object.userAccount);
                    this.permission$.next(['lessor']);
                    this.modal.isOperable$.next(true);
                    this.modal.closeModal();
                }
                //错误返回
                else {
                    alert(resp.json().message);
                    this.modal.isOperable$.next(true);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
                this.modal.isOperable$.next(true);
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
                this.modal.isOperable$.next(true);
            }
        });
    }

    /**拦截登录验证 */
    interceptorLogin(password) {
        if (localStorage.baseInfo && JSON.parse(localStorage.baseInfo).passWord) {
            if (password == JSON.parse(localStorage.baseInfo).passWord) {
                this.modal.isOperable$.next(true);
                this.isLogin = true;
                if (this.isLogin && this.redirectUrl) {
                    // let redirect = this.redirectUrl ? this.redirectUrl : '/login';
                    this.router.navigate([this.redirectUrl]);
                }
                else {
                    this.isLogin = false;
                    this.modal.closeModal();
                }
            }
            else {
                this.isLogin = false;
                alert("密码错误");
            }
        }
        else {
            this.isLogin = true;
            this.modal.isOperable$.next(true);
            localStorage.baseInfo = JSON.stringify({ passWord: password });
            this.router.navigate([this.redirectUrl]);
        }
    }

    /**验证密码 */
    checkPassword(oldPassword: string) {
        let _passwordParams = {
            oldPassword: oldPassword
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PORTAL + 'user/verifyPassword', _passwordParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    this.isTruePassword$.next(resp.json().object);
                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**修改密码 */
    changePassword(newPassword: string) {
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.get(this.sysConfigs.SERVER_PORTAL + `user/updatePassword?password=${newPassword}`, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    if (resp.json().object) {
                        this.isSuccessChange$.next(true);
                    }
                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**同步上班收费记录 */
    uploadChargeRecord(){
        let adminUser = JSON.parse(localStorage.adminUser);
        console.log(adminUser);

        let nowTime = new Date().getTime();
        let adminUsera = {
            adminUserId: adminUser.adminUserId,
            parkId: adminUser.parkId,
            createId: adminUser.createId,
            createName: adminUser.createName,
            createTime: nowTime,
            updateId: adminUser.updateId,
            updateName: adminUser.updateName,
            updateTime: nowTime,
        }
        let _passwordParams = {
            list: [Object.assign(adminUsera, adminUsera)]
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'computerpass/synch', _passwordParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            //正确返回
            if (resp.json().status == 0) {
                // this.baseInfo$.next(resp.json().object);
            }
            //错误返回
            else {
                alert(resp.json().message);
            }
        }, (erro) => {
            //状态码为400+
            if (erro.status >= 400 && erro.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (erro.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**收费电脑数据同步 */
    getBaseInfo(baseInfo: any) {
        let adminUser = JSON.parse(localStorage.adminUser);
        console.log(adminUser);

        let nowTime = new Date().getTime();
        let adminUsera = {
            adminUserId: adminUser.adminUserId,
            parkId: adminUser.parkId,
            createId: adminUser.createId,
            createName: adminUser.createName,
            createTime: nowTime,
            updateId: adminUser.updateId,
            updateName: adminUser.updateName,
            updateTime: nowTime,
        }
        let _passwordParams = {
            list: [Object.assign(adminUsera, baseInfo)]
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'computerpass/synch', _passwordParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            //正确返回
            if (resp.json().status == 0) {
                // this.baseInfo$.next(resp.json().object);
            }
            //错误返回
            else {
                alert(resp.json().message);
            }
        }, (erro) => {
            //状态码为400+
            if (erro.status >= 400 && erro.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (erro.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**清除token */
    clearToken() {
        localStorage.removeItem('token');
    }
    /**清除流 */
    clearStream() {
        this.isTruePassword$.next(false);
        this.isSuccessChange$.next(false);
    }
}


