import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';

@Injectable()
export class RoadListDBService {
    private db: any = null;
    private connection = indexedDB.open('roadList_DB', 1);
    private storeName = "roadList";
    /**出场记录信息流 */
    roadList$ = new Subject<Array<any>>();
    constructor() {
        this.connection.onupgradeneeded = function (event) {
            this.db = event.target.result;
            /**创建月卡信息表 */
            this.db.createObjectStore(this.storeName, { keyPath: 'id', autoIncrement: true });
        }.bind(this);
        //连接数据库成功后
        this.connection.onsuccess = function (event) {
            console.log("Success!");
            this.db = event.target.result;
        }.bind(this);
        //连接数据库失败后
        this.connection.onerror = function (e) {
            console.log("Error");
            console.dir(e);
        }.bind(this);
    }

    /**添加 */
    addData(data: any) {
        if (!this.db) {
            return;
        }
        console.log("addPublication OK");
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        // let count = store.count();
        // count.onsuccess = function (evt) {
        let req = store.add(data);
        req.onsuccess = (evt) => {
            this.getList();
            console.log("add success");
        };
        req.onerror = function () {
            console.error("add error", this.error);
        };
        // };
        // count.onerror = function (evt) {
        //     console.error("add error", this.error);
        // };
    }

    /**显示所有数据 */
    getList() {
        let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
        let req = store.count();
        req.onsuccess = function (evt) {
            console.log(evt.target.result);
        };
        req.onerror = function (evt) {
            console.error("add error", this.error);
        };
        req = store.openCursor();
        let outParkList = [];
        req.onsuccess = (evt) => {
            let cursor = evt.target.result;
            if (cursor) {
                let result = evt.target.result.value;
                let item: any = {
                    laneMarking: result.laneMarking,
                    entryMarking: result.entryMarking,
                    entryName: result.entryName,
                    controlNum: result.controlNum,
                    openedType: result.openedType,
                    isCardMachine: result.isCardMachine,
                    isMonitoring: result.isMonitoring,
                    inspectionMarking: result.inspectionMarking
                };
                outParkList.push(item);
                cursor.continue();
                this.roadList$.next(outParkList);
            }

        }
    }

    /**查询数据 */
    query(k) {
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        let req = store.get(k);
        req.onsuccess = (e) => {
            let result = e.target.result;
            let outParkList = [];
            let outParkItemt = {
                id: result.id,
                name: result.name,
                type: result.type,
                dep: result.dep,
                license: result.license,
                balance: result.balance,
            };
            outParkList.push(outParkItemt);
            this.roadList$.next(outParkList);
        };
    }


    /**删除数据库 */
    clearDB() {
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        store.clear();
        this.getList();
    }

    /**通过控制机号获得车道名称 */
    getEntryNameByEntryMarking(entryMarking): Observable<any> {
        /**数据库长度 */      
        return Observable.create((ob) => {
            let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);            
            let len;
            let req = store.count();
            let obj = {
                isExist: false,
                result: null
            };
            req.onsuccess = (evt) => {
                len = evt.target.result;

                if (len == 0) {
                    ob.next(obj);
                }
                else {
                    let request = store.openCursor();
                    let querynumber = 0;
                    request.onsuccess = (evt) => {
                        let cursor = evt.target.result;
                        if (cursor) {
                            let result = evt.target.result.value;
                            if (entryMarking == result.entryMarking && obj.isExist == false) {
                                obj = {
                                    isExist: true,
                                    result: result.entryName
                                }
                                ob.next(obj);
                                querynumber +=1;  
                            }
                            else{
                                querynumber +=1;                               
                            }
                            if (querynumber == len && obj.isExist == false) {
                                ob.next(obj);
                                
                            }
                            cursor.continue();
                        }
                    }
                    request.onerror = (evt) => {
                        ob.next(obj);
                    }
                }
            }
            req.onerror = (evt)=>{
                ob.next(obj);                
            };
        });
    }

}

export interface cardItem {
    adminUserId: string,
    parkId: string,
    createId: string,
    createName: string,
    createTime: number,
    updateTime: number,
    updateId: string,
    updateName: string,
    cardID: string, //卡片ID
    cardNo: string, //卡片号码
    cardType: string, // 卡片类型
    carNumber: string, // 车牌号码
    ownerName: string, // 车主姓名
    comeTime: number, // 入场时间
    comePark: string, // 入场车场
    gateNumber: string, // 入场道闸机号
    comePhoto: string, // 入场图片
    certificatePhoto: string, // 证件图片
    cardBalance: number, // 卡上余额
    remark: string // 备注


}


