import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { ModalService } from '../../services/modal/modal.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  constructor(private user: UserService, private modal: ModalService) { }

  /**按钮字 */
  buttonWord = "登录";
  isOperable = true;
  ngOnInit() {
    this.modal.isOperable$.subscribe((isAble) => {
      if (isAble == true) {
        this.buttonWord = "登录";
        this.isOperable = true;
      }
      else {
        this.buttonWord = `登录中....`;
        this.isOperable = false;
      }
    });
  }

  login(account, password) {
    this.modal.isOperable$.next(false);
    this.user.login(account.value, password.value);
    setTimeout(() => {
      if (this.isOperable == false) {
        this.modal.isOperable$.next(true);
        alert("登录超时");
      }
    }, 5000);
  }
}



