import { Component, OnInit } from '@angular/core';
import { RoadService } from '../../services/road/road.service';
import { RoadListDBService } from '../../services/DB/roadListDB';
import { UserService } from '../../services/user.service';
import { ParkingDictionary } from '../../services/constants/parkingDictionary';
import { DeviceService } from '../../services/device/device.service';

@Component({
  selector: 'app-menu-setRoad',
  templateUrl: './menu-setRoad.component.html',
  styleUrls: ['./menu-setRoad.component.scss']
})
export class MenuSetRoadComponent implements OnInit {
  /**tab */
  parking_panel = {
    isShow: false,
    parkingOpt: {
      // rechargeType: 0,       //收费方式
      // discountAllow: 0,      //是否允许折扣
      // discountType: "",//折扣方式
      // chargeDecimal: 0,//收费是否有小数
      // monthlyOverFee: 0,//月卡过期是否收费
      // isFreeTemporaryCard: 0,//临时车是否允许免费放行
      // noCardRelease: 0,//是否禁止无卡放行
      // IDControlInOrOut: 0,//是否ID控制进出
      // IDNoGetRead: 0,//是否ID卡不压地感读卡
      // temporaryCardOutCancelled: 0,//是否ID临时卡出场可以取消
      // realTimeDownloadValidCard: 0,//是否实时下载有效卡号
      // monthCardExpireTipNum: 0,//月卡到期提示天数
      // showShopTime: 0,//是否显示停车时间
      // broadcastCardNum: 0,//是否播报车牌
      // timeLoadingType: 0,//时间加载方式
      // timeInterval: "",//时间加载间隔
      // centerFeeTimeout: 0,//中心收费超时
      // centerTimeoutFee: "",//中心超时金额
      // showGateStatus: 0,//是否显示道闸状态
      // showCardMachineStatus: 0,//是否显示卡机状态
      // inOrOutTimeInterval: "",//进出时间间隔
      // isCanFreeCars: 0,//是否可进出免费车
      // fullLightBox: "",//满位灯箱
      // fullDeviceNum: "",//满位机号
      // fullPort: 0,//满位串口
      // fullBanCardRead: 0,//是否满位禁止读卡
      // totalTruckSpaceNum: "",//总车位数
      // temporaryTruckSpaceNum: "", //临时车位数
      // fixationTruckSpaceNum: "",//固定车位数
      // showRemainingTruckSpaceNum: 0,//控制机是否显示剩余车位
      // rechargeScreen: "",//收费显屏
      // closedScreenPort: "",//收屏串口
      // showReleaseInformation: 0,//控制机是否显示发布信息
      // controlShowWeek: 0,//控制机是否显示星期
      // billPrinting: 0//收费票据是否打印
    },
    //折扣方式选项
    discountMethods: this.parkdic.discountMethods,
    //收费方式选项
    feeMethods: this.parkdic.feeMethods,
    //加载时间方式选项      
    loadTimeMethods: this.parkdic.loadTimeMethods,
    /*模态框显示和隐藏*/
    coverShow:false
  };
  road_panel = {
    isShow: false,
    roadArray: [],
    gateFlags: this.parkdic.gateFlags,
    cotrollers: [],
    gates: [],
    openMethods: this.parkdic.openMethods,
  };
  
  // roadArray;
  // /**出入标记 */
  // flags;
  // /**控制机号 */
  // cotrollers;
  // /**开闸机号 */
  // gates;
  // /**开闸方式 */
  // methods;
  /*新增数据*/
  addObject={
    addcontroller:"",
    openWay:"",
    addRoadNumber:"",
    entranceAndExit_Identification:"",
    entranceAndExit:"",
    Monitor:false,
    checkCard:false,
    getCard:false,
    addControllerArray:[
      {
        label: '控制机1',
        value: '1'
      },
      {
        label: '控制机2',
        value: '2'
      }
    ]
  };
  // addcontroller:any;
  // openWay:any;
  // addRoadNumber:any;
  // entranceAndExit:any;
  // Monitor:any;
  // checkCard:any;
  // getCard:any;
  // addControllerArray=[
  //   {
  //     label: '控制机1',
  //     value: '1'
  //   },
  //   {
  //     label: '控制机2',
  //     value: '2'
  //   }
  // ];
  // addController
  constructor(private road: RoadService, private roadDB: RoadListDBService, private user: UserService, private parkdic: ParkingDictionary,private device: DeviceService) {
    // this.road_panel.roadArray = [];
    //当车道信息更新时，覆盖本地数据库
    this.road.roadList$.subscribe((roadObj) => {
      this.road_panel.roadArray = roadObj.collection;
      //覆盖数据
      this.roadDB.clearDB();
      roadObj.collection.forEach(element => {
        this.roadDB.addData(element);
      });
    });
    //当车场信息更新时，覆盖本地数据库
    this.road.parkOptList$.subscribe((parkOptList) => {
      this.parking_panel.parkingOpt = parkOptList;
    });
        /*获取控制机信息*/
        this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op === "ret-hb").subscribe((data) => {
          this.road_panel.cotrollers = [];
          data.hw.forEach(element => {
            this.road_panel.cotrollers.push(element.hw_id);
          });
        });
    // this.device.cotrollers
  }

  ngOnInit() {
    this.user.isLogin = false;
    this.parking_panel.isShow = true;
    this.road.getParkingOptList();
    this.road.getRoadList(1);
  }
  add(){
    console.log(this.addObject);

    this.road.getParksettingAddData(this.addObject);
  }
  submit() {
    console.log(this.road_panel.roadArray);
  }

  getList() {
    this.road.getRoadList(1);
    console.log(1221);

  }

  /*加载时间选项卡结束*/
  changeTabs(labelName) {
    this.closeAllTabs();
    switch (labelName) {
      case 'parking_panel':
        this.parking_panel.isShow = true;
        return;
      case 'road_panel':
        this.road_panel.isShow = true;
        return;
      default:
        this.parking_panel.isShow = true;
        return;
    }

  }
  closeAllTabs() {
    this.parking_panel.isShow = false;
    this.road_panel.isShow = false;
  }
}



