import { Injectable } from '@angular/core';
import { Router } from "@angular/router";
import { ModalService } from '../../services/modal/modal.service';
import { ParkingService } from '../../services/parking/parking.service';
import { DeviceService } from '../../services/device/device.service';
import { ChangePasswordComponent } from '../../popover/changePassword/changePassword.component';
import { CardManageService } from '../../services/cardManage/cardManage.service';

@Injectable()
export class CtrlbuttonService {

    constructor(private router: Router, private modal: ModalService,private parking: ParkingService,private device:DeviceService,private card: CardManageService) {
        document.onkeydown = (e) => {
            // 按 F3 车牌预置 
            if (e && e.keyCode == 114) {
                this.handle('setCard');
            }
            // 按 F4  取消配置
            else if (e && e.keyCode == 115) {
                this.handle('removeCard');
            }
            // 按 F5  无卡放行
            else if (e && e.keyCode == 116) {
                this.handle('release');
            }
            // 按 F6  卡机出卡
            else if (e && e.keyCode == 117) {
                this.handle('outCard');
            }
        };
    }

    /**根据按钮名字执行各自的函数 */
    handle(buttonName: string) {
        switch (buttonName) {
            /**修改密码 */
            case 'changePassword':
                this.router.navigate(['./changePassword']);
                this.modal.showModal();
                return;

            /**换班登录 */
            case 'login':
                this.router.navigate(['./login']);
                this.modal.showModal();
                return;

            /**车牌预置-F3 */
            case 'setCard':
                // this.router.navigate(['./setCard']);
                // this.modal.showModal();
                return;

            /**取消配置-F4 */
            case 'removeCard':
                console.log('setCard');
                // this.device.device_ws.send(JSON.stringify({"fid":"0", "op":"hb","id_from":"001","id_to":"002","ftime":"2017-12-1513:45:09"}));
                return;

            /**无卡放行-F5 */
            case 'release':
                this.router.navigate(['./release']);
                this.modal.showModal();
                // this.device.sendOpenGate(1);
                return;

            /**卡机出卡-F6 */
            case 'outCard':
                console.log('setCard');
                return;

            /**转到六画面 */
            case 'transform':
                // console.log('setCard');
                // this.
                return;

            /**卡片检测 */
            case 'checkCard':
                this.router.navigate(['./checkCard']);
                this.modal.showModal();
                return;

            /**月卡期限查询 */
            case 'searchCard':
                this.card.getCardInfo(1,"query");
                this.router.navigate(['./searchCard']);
                this.modal.showModal();
                return;

            /**场内车辆查询 */
            case 'searchCar':
            this.parking.getParkingCarList();
                this.router.navigate(['./searchCar']);
                this.modal.showModal();
                return;

            /**收费记录查询 */
            case 'searchRecord':
            this.parking.getParkingCarChargeList(1);
                this.router.navigate(['./searchRecord']);
                this.modal.showModal();
                return;

            default:
                return;
        }
    }
}


