import { Injectable } from '@angular/core';

@Injectable()
export class CommonService {
    constructor() {
    }

    timeTransform(date:number): any {
        let days;
        let hours;
        let minutes;
        // let seconds;
        let obj;
        if(date >= 86400000){
          let temp = 0;
          days = Math.floor((date - temp)/86400000);
          temp += days * 86400000;
          hours = Math.floor((date - temp)/3600000);
          temp += hours * 3600000;
          minutes = Math.floor((date - temp)/60000);
          return obj = {
            day:days,
            hour:hours,
            min:minutes
          }
        }
        else if(date < 86400000 && date >= 3600000){
          let temp = 0;
          hours = Math.floor((date - temp)/3600000);
          temp += hours * 3600000;
          minutes = Math.floor((date - temp)/60000);
          return obj = {
            day:"0",
            hour:hours,
            min:minutes
          }
        }
        else if(date < 3600000 && date >= 60000){
          let temp = 0;
          minutes = Math.floor((date - temp)/60000);
          return obj = {
            day:"0",
            hour:"0",
            min:minutes
          }
        }
        else{
            return obj = {
                day:"0",
                hour:"0",
                min:"0"
              }
        }
    }

    /**去除前面的0 */
    deleteZero(string:string){
        let arr = string.split("");
        let newArr:Array<any> = [];
        let sign;
        let total;
        for(let i = 0;i<arr.length;i++){
            if(arr[i] != "0"){
                sign = i;
                total = arr.length-sign;
                break;
            }
        }
        for(let i = 0;i<total;i++){
            newArr.push(arr[sign++]);
        }
        return newArr.join("");
    }


}

