import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
@Injectable()
export class ModalService {
    constructor() { }
    /**modal弹出流 */
    isShowModal$ = new BehaviorSubject(false);
    /**modal是否禁用流 */
    isOperable$ = new BehaviorSubject(true);
    showModal() {
        this.isOperable$.subscribe((isAble)=>{
            if(isAble == true){
                this.isShowModal$.next(true);
            }
        });
    }

    closeModal() {
        this.isOperable$.subscribe((isAble)=>{
            if(isAble == true){
                this.isShowModal$.next(false);
            }
        });
    }
}


