import { Directive, Input, TemplateRef, ViewContainerRef } from '@angular/core';
import { UserService } from '../services/user.service';

@Directive({
  selector: '[permission]'
})
export class PermissionDirective {
  perArr = ['all'];
  condition=[];
  constructor(private templateRef: TemplateRef<any>, private viewContainer: ViewContainerRef,private user:UserService) {
    this.user.permission$.subscribe((permission)=>{
      this.perArr = [...permission];
      this.construct();
    });
  }

  @Input() set permission(condition: Array<string>) {
    this.condition = condition;
    this.construct();
  }

  construct(){
    this.viewContainer.clear();
    let isCreate = false;
    for (let j = 0; j < this.perArr.length; j++) {
      for (let i = 0; i < this.condition.length; i++) {
        if (this.condition[i] == this.perArr[j] && isCreate == false) {
          this.viewContainer.createEmbeddedView(this.templateRef);
          isCreate = true;
          break;
        }
      }
    }
  }

}


