import { Injectable } from '@angular/core';
import { SysConfigs } from '../constants/sysConfigs';
import { Http, Response, Headers, URLSearchParams, RequestOptions } from '@angular/http';
import { BehaviorSubject, Subject } from 'rxjs'
import { DeviceService } from '../device/device.service';
@Injectable()
export class PrivilegeService {
    /**优惠规则流 */
    privilegeList$ = new Subject<any>();
    constructor(private http: Http, private sysConfigs: SysConfigs) { }

    /**查询优惠规则 */
    getPrivilegeList(map = {}) {
        let _searchParams = {
            parkId:JSON.parse(localStorage.adminUser).parkId
            // page: page,
            // pageSize: 10,
            // map: map,
            // sort: 'outTime',
            // dir: 'desc'
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'freerule/listAllToJsonForApp', _searchParams, headers).subscribe((resp: Response) => {
            //正确返回
            if (resp.json().status == 0) {
                this.privilegeList$.next(resp.json().object.freerule);
            }
            //错误返回
            else {
                alert(resp.json().message);
            }
        }, (error) => {
            if (error.status >= 400 && error.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (error.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }
}


