import { Injectable } from '@angular/core';
import { Subject } from 'rxjs'
import { RenderService } from '../render/render.service';

@Injectable()
export class PrivilegedbService {
    connection = indexedDB.open('privilegeList_DB', 1);
    private db: any = null;
    private storeName = "privilege";
    /**优惠信息流 */
    privilegeList$ = new Subject<Array<privilegeItem>>();
    constructor(private render: RenderService) {
        this.connection.onupgradeneeded = function (event) {
            this.db = event.target.result;
            this.store = this.db.createObjectStore(this.storeName, { keyPath: 'id', autoIncrement: true });

        }.bind(this);
        //连接数据库成功后
        this.connection.onsuccess = function (event) {
            console.log("Success!");
            this.db = event.target.result;
        }.bind(this);
        //连接数据库失败后
        this.connection.onerror = function (e) {
            // console.log("Error" + e.target.errorCode);
            console.dir(e);
            console.dir(this.connection);
            console.dir(this.db);
        }.bind(this);

    }


    /**添加 */
    addData(data: privilegeItem) {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        let req = store.add(data);
        req.onsuccess = (evt) => {
            this.getList();
        };
        req.onerror = function () {
            console.error("add error", this.error);
        };
    }

    /**批量添加数据 */
    addDatas(datas: Array<any>) {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        datas.forEach((data)=>{
            let req = store.add(data);
            req.onsuccess = (evt) => {
                // this.getList();
            };
            req.onerror = function () {
                console.error("add error", this.error);
            };
        });
        this.getList();
        
    }

    /**显示所有数据 */
    getList() {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
        /**数据库长度 */
        let len;
        let req = store.count();
        req.onsuccess = (evt) =>{
            len = evt.target.result;
            let privilegeList = [];
            if (len == 0) {
                this.privilegeList$.next(privilegeList);
            }
            else {
                let request = store.openCursor();
                request.onsuccess = (evt) => {
                    let cursor = evt.target.result;
                    if (cursor) {
                        let result = cursor.value;
                        let item: privilegeItem = {
                            adminUserId: result.adminUserId,
                            parkId: result.parkId,
                            createId: result.createId,
                            createName: result.createName,
                            createTime: result.createTime,
                            updateTime: result.updateTime,
                            updateId: result.updateId,
                            updateName: result.updateName,
                            freeName:result.freeName,
                            freeTime:result.freeTime,           
                            unitPrice:result.unitPrice,
                            unit:result.unit,
                            singleUseMax:result.singleUseMax,
                            superpositionType:result.superpositionType
                        };
                        privilegeList.push(item);
                        cursor.continue();
                        if (privilegeList.length == len) {
                            this.privilegeList$.next(privilegeList);
                        }
                    }
                    request.onerror = function (evt) {
                        console.error("add error", this.error);
                    };
                }
            }
        }
        req.onerror = function (evt) {
            console.error("add error", this.error);
        };


    }

    /**查询数据 */
    query(k) {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        let req = store.get(k);
        let privilegeList = [];
        req.onsuccess = (e) => {
            let result = e.target.result;
            let inParkItemt = {
                id: result.id,
                name: result.name,
                type: result.type,
                dep: result.dep,
                license: result.license,
                balance: result.balance,
            };
            privilegeList.push(inParkItemt);
            this.privilegeList$.next(privilegeList);
        };
    }


    /**删除数据库 */
    clearDB() {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        store.clear();
        this.getList();
    }
}

export interface privilegeItem {
    adminUserId: string;
    parkId: string;
    createId: string;
    createName: string;
    createTime: any;
    updateTime: any;
    updateId: string;
    updateName: string;
    freeName:any;
    freeTime:any;              
    unitPrice:any;
    unit:any;
    singleUseMax:any;
    superpositionType:any;
}


