import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { Router } from "@angular/router";
import { ModalService } from '../../services/modal/modal.service';
import { DeviceService } from '../../services/device/device.service';
import { ParkingService } from '../../services/parking/parking.service';
import { NavbuttonService } from '../../services/navbutton/navbutton.service';
import { CardManageService } from '../../services/cardManage/cardManage.service';
import { UserService } from '../../services/user.service';

@Injectable()
export class MenuService {
    /**是否显示菜单流 */
    isShowMenu$ = new BehaviorSubject(false);
    constructor(private router: Router, private modal: ModalService, private device: DeviceService, private parking: ParkingService,
        private nav:NavbuttonService,private card: CardManageService,private user: UserService) { }
    /**根据菜单名字执行各自的函数 */
    handle(menuName: string) {
        switch (menuName) {
            /**修改密码 */
            case 'changePassword':
                this.router.navigate(['./changePassword']);
                this.modal.showModal();
                return;

            /**换班登录 */
            case 'login':
                this.router.navigate(['./login']);
                this.modal.showModal();
                return;
                
            /**设置 */ 
            case 'setBaseInfo':
            this.router.navigate(['./setBaseInfo']);
            this.modal.showModal();
            return;

            /**退出 */
            case 'quit':
            this.nav.app_close();
            return; 

            /**车场管理 - 控制机设置 */
            case 'setController':
                this.user.isLogin = false;
                this.router.navigate(['./setController']);
                this.modal.showModal();
                return;

            /**车场管理 - 车道设置 */
            case 'setRoad':
                this.user.isLogin = false;       
                this.router.navigate(['./setRoad']);
                this.modal.showModal();
                console.log(this.user.isLogin);
                
                return;

            /**车场管理 - 收费设置 */
            case 'setFeeStandard':
                this.router.navigate(['./setFeeStandard']);
                this.modal.showModal();
                return;

            /**车场管理 - 机号设置 */
            case 'setDeviceNo':
                this.router.navigate(['./setDeviceNo']);
                this.modal.showModal();
                return;

            /**停车管理 - 出场纪录 */
            case 'recordOut':
                this.parking.getOutParkList(1);
                this.router.navigate(['./recordOut']);
                this.modal.showModal();
                return;

            /**停车管理 - 入场纪录 */
            case 'recordIn':
                this.parking.getInParkList(1);
                this.router.navigate(['./recordIn']);
                this.modal.showModal();
                return;

            /**停车管理 - 无卡放行纪录 */
            case 'recordRelease':
                this.parking.getReleaseList(1);
                this.router.navigate(['./recordRelease']);
                this.modal.showModal();
                return;

            /**停车管理 - 场内车辆查询 */
            case 'recordCar':
                this.parking.getParkingCarList();
                this.router.navigate(['./recordCar']);
                this.modal.showModal();
                return;

            /**停车管理 - 车辆收费查询 */
            case 'recordCarCharge':
                this.parking.getParkingCarChargeList(1);
                this.router.navigate(['./searchRecord']);
                this.modal.showModal();
                return;

            /**卡片管理 - 卡片下载 */
            case 'cardDownload':
                this.router.navigate(['./cardDownload']);
                this.modal.showModal();
                return;

            /**卡片管理 - 月卡期限查询 */
            case 'cardQuery':
                this.card.getCardInfo(1,"query");
                this.router.navigate(['./cardQuery']);
                this.modal.showModal();
                return;

            /**数据同步 - 入场纪录 */
            case 'updateInParking':
                this.router.navigate(['./updateInParking']);
                this.modal.showModal();
                return;

            /**数据同步 - 出场纪录 */
            case 'UpdateOutParking':
                this.router.navigate(['./updateOutParking']);
                this.modal.showModal();
                return;

            /**数据同步 - 无卡放行记录 */
            case 'updateRelease':
                this.router.navigate(['./updateRelease']);
                this.modal.showModal();
                return;

            /**数据同步 - 月卡信息 */
            case 'updateCardInfo':
                this.router.navigate(['./updateCardInfo']);
                this.modal.showModal();
                return;

            /**数据同步 - 车道信息 */
            case 'updateRoadInfo':
                this.router.navigate(['./updateRoadInfo']);
                this.modal.showModal();
                return;

            /**数据同步 - 优惠信息 */
            case 'updatePrivilege':
                this.router.navigate(['./updatePrivilege']);
                this.modal.showModal();
                return;
                
            default:
                return;
        }
    }
}


