import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { RenderService } from '../render/render.service';

@Injectable()
export class OutparkdbService {
    private db: any = null;
    private connection = indexedDB.open('outParkList_DB', 1);
    private storeName = "outPark";

    /**出场记录信息流 */
    outParkList$ = new Subject<Array<outParkItem>>();
    constructor(private render: RenderService) {
        this.connection.onupgradeneeded = function (event) {
            this.db = event.target.result;
            /**创建两个表：出场纪录表和入场纪录表 */
            this.db.createObjectStore(this.storeName, { keyPath: 'id', autoIncrement: true });
        }.bind(this);
        //连接数据库成功后
        this.connection.onsuccess = function (event) {
            console.log("Success!");
            this.db = event.target.result;
        }.bind(this);
        //连接数据库失败后
        this.connection.onerror = function (e) {
            console.log("Error");
            console.dir(e);
        }.bind(this);
    }

    /**添加 */
    addData(data: outParkItem) {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        let req = store.add(data);
        req.onsuccess = (evt) => {
            this.getList();
        };
        req.onerror = function () {
            console.error("add error", this.error);
        };
    }

    /**批量添加数据 */
    addDatas(datas: Array<outParkItem>) {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        datas.forEach((data) => {
            let req = store.add(data);
            req.onsuccess = (evt) => {
                this.getList();
            };
            req.onerror = function () {
                console.error("add error", this.error);
            };
        });
    }

    /**显示所有数据 */
    getList() {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
        /**数据库长度 */
        let len;
        let req = store.count();
        req.onsuccess = (evt) => {
            len = evt.target.result;
            let outParkList = [];
            if (len == 0) {
                this.outParkList$.next(outParkList);
            }
            else {
                let request = store.openCursor();
                request.onsuccess = (evt) => {
                    let cursor = evt.target.result;
                    if (cursor) {
                        let result = cursor.value;
                        let item: outParkItem = {
                            adminUserId: result.adminUserId,
                            parkId: result.parkId,
                            createId: result.createId,
                            createName: result.createName,
                            createTime: result.createTime,
                            updateTime: result.updateTime,
                            updateId: result.updateId,
                            updateName: result.updateName,
                            cardID: result.cardID, //卡片ID
                            cardNo: result.cardNo, //卡片号码
                            cardType: result.cardType, // 卡片类型
                            carNumber: result.carNumber, // 车牌号码
                            ownerName: result.ownerName, // 车主姓名
                            comeTime: result.comeTime, // 入场时间
                            comePark: result.comePark, // 入场车场
                            gateNumber: result.gateNumber, // 入场道闸机号
                            comePhoto: result.comePhoto, // 入场图片
                            certificatePhoto: result.certificatePhoto, // 证件图片
                            cardBalance: result.cardBalance, // 卡上余额
                            outTime: result.outTime, // 出场时间
                            outPark: result.outPark, // 出场车场
                            outGateNumber: result.outGateNumber, // 出场道闸机号
                            outPhoto: result.outPhoto, // 出场图片
                            feeMoney: result.feeMoney, // 收费金额
                            amountReceive: result.amountReceive, // 应收金额
                            timeoutFlag: result.timeoutFlag, // 超时标志
                            timeoutHour: result.timeoutHour, // 超时收费时间
                            timeoutMoney: result.timeoutMoney, // 超时收费金额
                            freeReason: result.freeReason, // 免费原因
                            freeReasonId: result.freeReasonId, // 免费原因Id
                            remark: result.remark, // 备注
                            recordId: result.recordId
                        };
                        outParkList.push(item);
                        cursor.continue();
                        if (outParkList.length == len) {
                            this.outParkList$.next(outParkList);
                            this.exportData(outParkList);
                        }
                    }
                    request.onerror = function (evt) {
                        console.error("add error", this.error);
                    };
                }
            }
        }
        req.onerror = function (evt) {
            console.error("add error", this.error);
        };
    }

    /**查询数据 */
    query(record_id) {
        return Observable.create((ob) => {
            let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
            let len;
            let req = store.count();
            req.onsuccess = (evt) => {
                len = evt.target.result;
                let isExist = false;
                if (len == 0) {
                    ob.next(isExist);
                }
                else {
                    let request = store.openCursor();
                    let querynumber = 0;
                    request.onsuccess = (evt) => {
                        let cursor = evt.target.result;
                        if (cursor) {
                            let result = evt.target.result.value;
                            if (record_id == result.recordId && isExist == false) {
                                isExist = true;
                                ob.next(isExist);
                                querynumber += 1;
                            }
                            else if (record_id != result.recordId || isExist == true) {
                                querynumber += 1;
                            }
                            if (querynumber == len && isExist == false) {
                                ob.next(isExist);
                            }
                            cursor.continue();
                        }
                    }
                    request.onerror = (evt) => {
                        ob.next(isExist);
                    }
                }
            }
            req.onerror = function (evt) {
                console.error("add error", this.error);
            };
        });
    }

    /**删除数组里面的数据 */
    deleteDatas(record_id) {
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        let len;
        let req = store.count();
        req.onsuccess = (evt) => {
            len = evt.target.result;
            let isExist = false;
            if (len == 0) {
                return;
            }
            else {
                let request = store.openCursor();
                let querynumber = 0;
                request.onsuccess = (evt) => {
                    let cursor = evt.target.result;
                    if (cursor) {
                        let result = evt.target.result.value;
                        if (record_id == result.recordId && isExist == false) {
                            let dereq = cursor.delete();
                            querynumber += 1;
                            dereq.onsuccess = () => {
                            };
                            dereq.onerror = () => {
                                console.log("删除失败");
                            };
                        }
                        else if (record_id != result.recordId || isExist == true) {
                            querynumber += 1;
                        }
                        if (querynumber == len) {
                        }
                        cursor.continue();
                    }
                }
                request.onerror = (evt) => {
                }
            }
        }
        req.onerror = function (evt) {
            console.error("add error", this.error);
        };
    }

    /**删除数据库 */
    clearDB() {
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        store.clear();
        this.getList();
    }

        /**导出数据 */
        exportData(datas) {
            let data = {
                list: datas
            }
            this.render.send('save-db-outpark', JSON.stringify(data));
        }
}

export interface outParkItem {
    // cardID: string, // 卡片ID
    // cardNo: string, // 卡片号码
    // cardType: string, // 卡片类型
    // carNumber?: string, // 车牌号码
    // ownerName?: string, // 车主姓名
    // comeTime: string, // 入场时间
    // comePark?: string, // 入场车场
    // gateNumber?: string, // 道闸机号
    // comePhoto?: string, // 入场图片
    // certificatePhoto?: string, // 证件图片
    // cardBalance?: string, // 卡上余额
    // remark?: string // 备注   
    adminUserId: string,
    parkId: string,
    createId: string,
    createName: string,
    createTime: number,
    updateTime: number,
    updateId: string,
    updateName: string,
    recordId?: any,
    cardID?: string, //卡片ID
    cardNo: string, //卡片号码
    cardType: string, // 卡片类型
    carNumber?: string, // 车牌号码
    ownerName?: string, // 车主姓名
    comeTime: number, // 入场时间
    comePark?: string, // 入场车场
    gateNumber?: string, // 入场道闸机号
    comePhoto?: string, // 入场图片
    certificatePhoto?: string, // 证件图片
    cardBalance?: number, // 卡上余额
    outTime: number, // 出场时间
    outPark: string, // 出场车场
    outGateNumber: string, // 出场道闸机号
    outPhoto?: string, // 出场图片
    feeMoney: number, // 收费金额
    amountReceive: number, // 应收金额
    timeoutFlag?: number, // 超时标志
    timeoutHour?: number, // 超时收费时间
    timeoutMoney?: number, // 超时收费金额
    freeReason?: string, // 免费原因
    freeReasonId?: string, // 免费原因Id
    remark?: string // 备注
}


