import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { ModalService } from '../../services/modal/modal.service';

@Component({
  selector: 'app-changePassword',
  templateUrl: './changePassword.component.html',
  styleUrls: ['./changePassword.component.scss']
})
export class ChangePasswordComponent implements OnInit {
  /**密码是否正确，密码是否相同 */
  isTruePassword = false;
  isSamePassword = false;
  password:string='';
  newPassword:string="";
  confirmPassword:string="";
  constructor(private user:UserService,private modal:ModalService) { }

  ngOnInit() {
    this.user.isTruePassword$.subscribe((isTrue)=>{
      this.isTruePassword = isTrue;
    });
    this.user.isSuccessChange$.subscribe((isSuccess)=>{
      if(isSuccess==true){
        this.modal.isOperable$.next(true);
        this.modal.closeModal();
        this.clearData();
        this.user.clearStream();
      }
    });
  }
  /**检查密码是否正确 */
  checkPassword(){
    this.user.checkPassword(this.password);
    // alert(112);
  }
  /**检查密码是否相同 */
  samePassword(){
    this.isSamePassword = this.newPassword== this.confirmPassword?true:false;
  }
  /**如果验证通过则修改密码 */
  changeNewPassword(){
    if(this.isTruePassword==true&&this.isSamePassword==true){
      this.user.changePassword(this.newPassword);
      this.modal.isOperable$.next(false);
    }
  }

  clearData(){
    this.password='';
    this.newPassword="";
    this.confirmPassword="";
  }
}



