import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-menu-setFeeStandard',
  templateUrl: './menu-setFeeStandard.component.html',
  styleUrls: ['./menu-setFeeStandard.component.scss']
})
export class MenuSetFeeStandardComponent implements OnInit {
  standard={
    type:'临时卡',
    topFee:10000,
    freeTime_min:60,
    isFeeInFree:false
  };
  types=[
    '临时卡','月卡'
  ];
  body =[];
  constructor() { }

  ngOnInit() {
    for(let i=0;i<24;i++){
      let item = {
        hour:i+1,
        fee:""
      };
      this.body.push(item);
    }
  }

}



