import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginGuardService } from './services/loginGuard/loginGuard.service';

/**左侧按钮 */
import { LoginComponent } from './popover/login/login.component';
import { ChangePasswordComponent } from './popover/changePassword/changePassword.component';
import { SetCardComponent } from './popover/setCard/setCard.component';
import { CheckCardComponent } from './popover/checkCard/checkCard.component';
import { SearchCarComponent } from './popover/searchCar/searchCar.component';
import { SearchCardComponent } from './popover/searchCard/searchCard.component';
import { SearchRecordComponent } from './popover/searchRecord/searchRecord.component';
import { ReleaseWithoutCardComponent } from './popover/releaseWithoutCard/releaseWithoutCard.component';

/**菜单 */
import { InterceptorComponent } from './popover/interceptor/interceptor.component';

import { MenuSetRoadComponent } from './menupages/menu-setRoad/menu-setRoad.component';
import { MenuSetControllerComponent } from './menupages/menu-setController/menu-setController.component';
import { MenuSetFeeStandardComponent } from './menupages/menu-setFeeStandard/menu-setFeeStandard.component';
import { MenuSetDeviceNoComponent } from './menupages/menu-setDeviceNo/menu-setDeviceNo.component';
import { MenuSetBaseInfoComponent } from './menupages/menu-setBaseInfo/menu-setBaseInfo.component';

import { MenuRecordInComponent } from './menupages/menu-recordIn/menu-recordIn.component';
import { MenuRecordOutComponent } from './menupages/menu-recordOut/menu-recordOut.component';
import { MenuRecordReleaseComponent } from './menupages/menu-recordRelease/menu-recordRelease.component';
// import { MenuRecordCarComponent } from './menupages/menu-recordCar/menu-recordCar.component';

import { MenuCardDownloadComponent } from './menupages/menu-cardDownload/menu-cardDownload.component';
// import { MenuCardQueryComponent } from './menupages/menu-cardQuery/menu-cardQuery.component';

import { MenuUpdateInParkingComponent } from './menupages/menu-updateInParking/menu-updateInParking.component';
import { MenuUpdateOutParkingComponent } from './menupages/menu-updateOutParking/menu-updateOutParking.component';
import { MenuUpdateReleaseComponent } from './menupages/menu-updateRelease/menu-updateRelease.component';
import { MenuUpdateCardInfoComponent } from './menupages/menu-updateCardInfo/menu-updateCardInfo.component';
import { MenuUpdateRoadInfoComponent } from './menupages/menu-updateRoadInfo/menu-updateRoadInfo.component';
import { MenuUpdatePrivilegeComponent } from './menupages/menu-updatePrivilege/menu-updatePrivilege.component';

/**第二层路由 */
// const modalChildRouters: Routes = [
//     { path: "login", component: LoginComponent },
//     // {path: "two", component: ListTwoComponent},
//     // 如果地址栏中输入没有定义的路由就跳转到one路由界面
//     { path: '**', redirectTo: "login" },
//     { path: '', redirectTo: 'login', pathMatch: 'full' },
// ];


/**第一层路由 */
// const routers: Routes = [
//     { path: 'main', component: MainComponent },
//     { path: 'modal', component: ModalComponent, children: modalChildRouters },
//     // {path:'login',component:LoginComponent},
//     { path: '', redirectTo: 'main', pathMatch: 'full' },
//     { path: '**', redirectTo: 'main' },
// ];

const routers: Routes = [
    /**左侧按钮 */
        { path: 'login', component: LoginComponent },
        { path: 'changePassword',component:ChangePasswordComponent },
        { path: 'setCard',component:SetCardComponent },
        { path: 'checkCard',component:CheckCardComponent },
        { path: 'searchCar',component:SearchCarComponent },
        { path: 'searchCard',component:SearchCardComponent },
        { path: 'searchRecord',component:SearchRecordComponent },
        { path: 'release',component:ReleaseWithoutCardComponent },
        
        /**菜单  */
        { path: 'setController', component: MenuSetControllerComponent,canActivate:[LoginGuardService] },
        { path: 'setRoad',component:MenuSetRoadComponent,canActivate:[LoginGuardService] },
        { path: 'setBaseInfo',component:MenuSetBaseInfoComponent,canActivate:[LoginGuardService] },
        // { path: 'setFeeStandard',component:MenuSetFeeStandardComponent },
        // { path: 'setDeviceNo',component:MenuSetDeviceNoComponent },
        { path: 'interceptor',component:InterceptorComponent },
        
        { path: 'recordIn',component:MenuRecordInComponent },
        { path: 'recordOut',component:MenuRecordOutComponent },
        { path: 'recordRelease',component:MenuRecordReleaseComponent },
        { path: 'recordCar',redirectTo: 'searchCar', pathMatch: 'full' },

        { path: 'cardQuery',redirectTo: 'searchCard', pathMatch: 'full' },
        { path: 'cardDownload',component:MenuCardDownloadComponent },

        { path: 'updateInParking',component:MenuUpdateInParkingComponent },
        { path: 'updateOutParking',component:MenuUpdateOutParkingComponent },
        { path: 'updateRelease',component:MenuUpdateReleaseComponent },
        { path: 'updateCardInfo',component:MenuUpdateCardInfoComponent },
        { path: 'updateRoadInfo',component:MenuUpdateRoadInfoComponent },
        { path: 'updatePrivilege',component:MenuUpdatePrivilegeComponent },
        
        { path: '', redirectTo: 'login', pathMatch: 'full' },
        { path: '**', redirectTo: 'login' },
    ];

@NgModule({
    imports: [
        RouterModule.forRoot(routers, { enableTracing: true })
        // RouterModule.for(routers,{ enableTracing: true })
    ],
    exports: [
        RouterModule
    ]
})
export class AppRoutingModule { }


