import { Component, OnInit } from '@angular/core';
import { DeviceService } from '../../services/device/device.service';
import { RoadListDBService } from '../../services/DB/roadListDB';
import { ParkingService } from '../../services/parking/parking.service';
import { ReleasedbService, releaseItem } from '../../services/DB/releaseListDB';

@Component({
  selector: 'app-releaseWithoutCard',
  templateUrl: './releaseWithoutCard.component.html',
  styleUrls: ['./releaseWithoutCard.component.scss']
})
export class ReleaseWithoutCardComponent implements OnInit {
  controllerTimeparamsArray: Array<any> = [];
  constructor(private device: DeviceService, private roadDB: RoadListDBService, private parking: ParkingService, private releaseDB: ReleasedbService) {
    //接收到心跳回馈
    this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op === "ret-hb").subscribe((data) => {
      if (data.hw && data.hw.length > 0) {
        this.controllerTimeparamsArray = [];
        data.hw.forEach(element => {
          this.roadDB.getEntryNameByEntryMarking(element.hw_id).subscribe((name) => {
            if (name.isExist == true) {
              element.name = name.result;
            }
            else {
              element.name = element.hw_id;
            }
          });
          this.controllerTimeparamsArray.push(element)
        });
      }
    });
    //接收到开闸回馈
    this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op === "ret-z").subscribe((data) => {
      if (data.v == 1) {
        //查询车道数据库将控制机号转化为车道        
        this.roadDB.getEntryNameByEntryMarking(data.hw_id).subscribe((name) => {
          if (name.isExist == true) {
            this.dataFormatToReleaseList(data, name.result);
          }
          else {
            this.dataFormatToReleaseList(data, data.hw_id);
          }
        });
      }
    });
    //接收到同步数据回馈
    this.parking.releaseListBack$.subscribe((back) => {
          //如果收到数据，则删除数据库里面相对应的数据
          if (back.isConnect == true) {
            back.data.forEach(element => {
              this.releaseDB.deleteDatas(element.recordId);
            });
            this.releaseDB.releaseList$.next([]);
            //如果此时有数据，则上传到云端
            this.releaseDB.releaseList$.filter(list=>list.length>0).subscribe((list) => {
                // this.parking.addReleaseList(list);
            });
            this.releaseDB.getList();
          }
          //如果网络连接不顺畅，就算了咯
          else {
    
          }
    });
  }
  ngOnInit() {
    /*通过sendHb方法得到订阅的数据*/
    this.device.sendHb();
  }
  open(item) {
    this.device.sendOpenGate(item.hw_id);
  }
  close(item) {
    this.device.sendCloseGate(item.hw_id);
  }

  /**转化为无卡放行纪录 */
  dataFormatToReleaseList(oldData, roadName) {
    let adminUser = JSON.parse(localStorage.adminUser);
    let nowTime = new Date().getTime();
    let newData: releaseItem = {
      adminUserId: adminUser.adminUserId,
      parkId: adminUser.parkId,
      createId: adminUser.createId,
      createName: adminUser.createName,
      createTime: nowTime, //+adminUser.createTime,
      updateTime: nowTime, //+adminUser.updateTime,
      updateId: adminUser.updateId,
      updateName: adminUser.updateName,
      comeTime: nowTime, // 入场时间
      comePark: roadName, // 入场车场
      gateNumber: oldData.hw_id, // 入场道闸机号
      recordId:`${oldData.hw_id}+${nowTime}`
    }
    this.parking.addReleaseList([newData]);
  }
}




