import { Component, OnInit } from '@angular/core';
import { ParkingService } from '../../services/parking/parking.service';

@Component({
  selector: 'app-searchCar',
  templateUrl: './searchCar.component.html',
  styleUrls: ['./searchCar.component.scss']
})
export class SearchCarComponent implements OnInit {
  // head;
  // body;
  // cardNo:string;
  // carNumber:string;

  // constructor(private parking: ParkingService) { }

  // ngOnInit() {
  //   this.head = ['卡片号码','卡片种类','入场时间','车牌号码','入场名称','入场操作员'];
  //   this.parking.parkingList$.subscribe((obj)=>{
  //     this.body = obj.collection;
  //     console.log(obj);

  //   });

  // }

  // getParkingCarList(){
  //   this.parking.getParkingCarList({
  //     cardNo: this.cardNo,
  //     carNumber: this.carNumber
  // });
  // }

  //   reset(){
  //     this.cardNo = "";
  //     this.carNumber = "";
  //   }

  /**搜索字段 */
  map = {
    cardNo:"",
    carNumber:""
  }

  /**表格参数 */
  head;
  body;
  filterBody;

  /**分页功能 */
  /**当前的页数 */
  currentIndex: number = 1;
  /**列表的长度，默认是6 */
  len: number = 6;
  /**标志位，记录当前列表的最小值 */
  minIndex;
  /**标志位，记录当前列表的最大值 */
  maxIndex;
  /**总共有几页 */
  maxPage;
  /**每页的条数 */
  itemNumber = 10;
  /**总共的条数 */
  totalSize;
  /**分页数组 */
  pageIndex: Array<number> = [];

  constructor(private parking: ParkingService) {

  }
  search() {
    this.parking.getParkingCarList(this.map);
  }

  ngOnInit() {
    this.head = ['卡号', '卡片种类', '入场时间', '车牌号码', '入场车道', '入场操作员'];

    this.parking.parkingList$.subscribe((data: any) => {
      this.body = data.collection;
      this.maxPage = Math.ceil(data.collection.length / this.itemNumber);
      this.totalSize = data.collection.length;
      this.len = Number(data.totalPage) > this.len ? this.len : Number(data.totalPage);
      this.currentIndex == 1?this.initPageIndex(this.len,1):false;
      this.filterArray(this.body, this.itemNumber, 1);
    });

  }



  reset() {
    this.map = {
      cardNo:"",
      carNumber:""
    }
  }

  /**格式化数组，接收参数是数组,每页的条数,输出的是第几页 */
  filterArray(array: Array<any>, itmes: number, page) {
    //返回可能的最大的索引号
    if (page > Math.ceil(array.length / itmes)) {
      page = Math.ceil(array.length / itmes);
    }
    this.filterBody = array.filter((value, index, array) => {
      if (index >= (page - 1) * itmes && index < page * itmes) {
        return array[index];
      }
    });
  }

  /**初始化 */
  initPageIndex(len: number, index = 1) {
    index = index < 1 ? 1 : index;
    this.minIndex = index;
    for (let i = 0; i < len; i++) {
      this.pageIndex[i] = index++;
      this.maxIndex = this.pageIndex[i];
    }
  }
  /**点击导航按钮 */
  indexNav(i, mode = "") {
    if (mode == "") {
      this.currentIndex = i;
    }
    else if (mode == "back") {
      this.currentIndex - 1 >= 1 ? this.currentIndex -= 1 : this.currentIndex = 1;
    }
    else if (mode == "next") {
      this.currentIndex + 1 <= this.maxPage ? this.currentIndex += 1 : this.currentIndex = this.maxPage;
    }
    //根据点击的按钮显示数据
    this.filterArray(this.body, this.itemNumber, this.currentIndex);
    //如果点击的数大于了当前最大的索引值
    if (this.currentIndex >= this.maxIndex) {
      this.initPageIndex(this.len, this.currentIndex);
    }
    //如果点击的数小于了当前最小的索引值    
    else if (this.currentIndex <= this.minIndex) {
      this.initPageIndex(this.len, this.currentIndex - this.len + 1);
    }
    //如果生成的索引数组小于规定的索引数组长度
    if (this.currentIndex + this.len > this.maxPage) {
      this.initPageIndex(this.len, this.maxPage - this.len + 1);
    }
  }
}



