import { Injectable } from '@angular/core';
import { SysConfigs } from '../constants/sysConfigs';
import { Http, Response, Headers, URLSearchParams, RequestOptions } from '@angular/http';
import { BehaviorSubject, Subject } from 'rxjs'
@Injectable()
export class CardManageService {
    /**月卡信息流 */
    cardInfo$ = new Subject<any>();
    /**月卡信息下载流 */
    cardInfoDownLoad$ = new Subject<any>();
    constructor(private http: Http, private sysConfigs: SysConfigs) { }
    /**查询卡片信息 */
    getCardInfo(page = 1,mode ="download", map: cardParams = {cardNo: "",carNumber: "",cardType: "",ownerName: "",cardStatus: "",expireDays: ""}) {
        let _searchParams = {
            map: map,
            page: page,
            pageSize: 10
        }

        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'cardinfo/list', _searchParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    if (resp.json().object.collection) {
                        // this.cardInfo$.next(resp.json().object.collection);
                        let obj = {
                            collection: resp.json().object.collection,
                            totalSize: resp.json().object.totalSize,
                            totalPage: Math.ceil(resp.json().object.totalSize / resp.json().object.pageSize)
                        }
                        mode=="download"?this.cardInfoDownLoad$.next(obj):this.cardInfo$.next(obj);
                    }
                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }
}

export interface cardParams {
    cardNo: string;
    carNumber: string;
    cardType: string;
    ownerName: string;
    cardStatus?: string;
    expireDays: string;
}


