import { Component, OnInit } from '@angular/core';
import { CardListDBService } from '../../services/DB/cardListDB';

@Component({
  selector: 'app-menu-updateCardInfo',
  templateUrl: './menu-updateCardInfo.component.html',
  styleUrls: ['./menu-updateCardInfo.component.scss']
})
export class MenuUpdateCardInfoComponent implements OnInit {
  body;
  head = ['序号', '卡号', '车牌号码', '类型', '车主姓名','手机号码','有效截至日期'];
  iiii = 0
  constructor(private cardDB: CardListDBService) { 
    this.cardDB.cardList$.subscribe((cardList)=>{
      this.body = cardList;
    });
  }

  ngOnInit() {
    this.cardDB.getList();
  }

}



