import { Component, OnInit } from '@angular/core';
import { WebsocketService } from '../../services/common.service';
import { CtrlbuttonService } from '../../services/ctrlbutton/ctrlbutton.service';

@Component({
  selector: 'app-controller-button',
  templateUrl: './controller-button.component.html',
  styleUrls: ['./controller-button.component.scss'],
  inputs: ['label', 'buttonName','permiss']
})
export class ControllerButtonComponent implements OnInit {
  label;
  buttonName;
  permiss=[];
  constructor(private ctrl: CtrlbuttonService) {
  }

  ngOnInit() {

  }

  runFunction() {
    this.ctrl.handle(this.buttonName);
  }
}



