import { Injectable } from '@angular/core';
import { SysConfigs } from '../constants/sysConfigs';
import { BehaviorSubject, Subject, Observable } from 'rxjs'
import { ParkingDictionary } from '../constants/parkingDictionary';
declare let ReconnectingWebSocket: any;
@Injectable()
export class DeviceService {
    // device_ws = new WebSocket(this.sysConfigs.DEVICE_SOCKET);
    device_ws;
    fid: number = 0;
    onmessage$ = new Subject<any>();
    state: boolean = false;
    constructor(private sysConfigs: SysConfigs, public parkdic: ParkingDictionary) {
        this.device_ws = new WebSocket(this.sysConfigs.DEVICE_SOCKET);
        this.onmessage$.filter(data => data.ret != "0").subscribe((data) => {
            //硬件通信异常
            if (data.ret > 0 && data.ret <= 100) {
                console.log("硬件通信异常");
            }
            //命令内容异常
            else if (data.ret > 100 && data.ret <= 200) {
                console.log("命令内容异常");
            }
        });

        this.device_ws.onopen = () => {
            this.state = true;
            console.log("与硬件服务器开始连接");
        }

        /**当信息返回*/
        this.device_ws.onmessage = (ret) => {
            this.onmessage$.next(JSON.parse(ret.data));
            // console.log(ret.data);
        }

        this.device_ws.onclose = () => {
            this.state = false;
            console.log('与硬件服务器断开连接');
            this.device_ws.close(); //关闭TCP连接
        }

        /**定时器 */
        Observable.interval(5000).subscribe(() => {

            if (this.state == true) {
                this.sendHb();
            }
            else {
                this.reconnect();
                // console.log(1212123322312123123);            
                // this.device_ws = new WebSocket(this.sysConfigs.DEVICE_SOCKET); 
            }
        });
    }

    reconnect() {
        this.device_ws = new WebSocket(this.sysConfigs.DEVICE_SOCKET);
        this.onmessage$.filter(data => data.ret != "0").subscribe((data) => {
            //硬件通信异常
            if (data.ret > 0 && data.ret <= 100) {
                alert("硬件通信异常");
            }
            //命令内容异常
            else if (data.ret > 100 && data.ret <= 200) {
                alert("命令内容异常");
            }
        });

        this.device_ws.onopen = () => {
            this.state = true;
            console.log("与硬件服务器开始连接");
        }

        /**当信息返回*/
        this.device_ws.onmessage = (ret) => {
            this.onmessage$.next(JSON.parse(ret.data));
            console.log(ret.data);
        }

        this.device_ws.onclose = () => {
            this.state = false;
            console.log('与硬件服务器断开连接');
            this.device_ws.close(); //关闭TCP连接
        }

    }

    /**发送心跳，用来检查线路是否通畅，每5秒检查一次，1秒内没收到正确回应则重新连接 */
    sendHb() {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'hb',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime())
        }
        this.device_ws.send(JSON.stringify(data));
        // console.log(data);
        this.timeStringToNumber("2017-12-15 13:45:09");
    }
    /**查询硬件时间 */
    sendGetTime(hw_id) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'get-hw-time',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            hw_id: hw_id.toString()                            //控制机ID            
        }
        this.device_ws.send(JSON.stringify(data));
    }
    /**修改硬件时间 */
    sendSetHwTime(hw_id) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'set-hw-time',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            hw_id: hw_id.toString(),                            //控制机ID
            v: this.timeNumberToString(new Date().getTime())    //时间
        }
        this.device_ws.send(JSON.stringify(data));
    }
    /**开闸*/
    sendOpenGate(hw_id) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'z',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            hw_id: hw_id.toString(),                            //控制机ID
            v: 1                                                //关闸
        }
        this.device_ws.send(JSON.stringify(data));
    }
    /**关闸 */
    sendCloseGate(hw_id) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'z',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            hw_id: hw_id.toString(),                            //控制机ID
            v: 0                                                //开闸
        }
        this.device_ws.send(JSON.stringify(data));
    }

    /**查询收费规则 */
    sendGetFeeRules(hw_id) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'price-get',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            hw_id: hw_id.toString(),
            card_type: "36"
        }
        this.device_ws.send(JSON.stringify(data));
    }

    /**修改收费规则 */
    sendSetFeeRules(hw_id, opt: feeRule) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'price-set',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            hw_id: hw_id.toString(),
            card_type: "36",
            fee_type: "1",
            free_m: opt.free_time.toString(),
            price: opt.priceArray.map((key, value, arr) => { return Math.round(Number(key)) }),
            free_m_no_charge: this.boolToString(opt.free_m_no_charge),
            day_limit: opt.day_limit
        }

        // console.log(opt.priceArray.map((key, value, arr) => {
        //     console.log(key);
        //     console.log(arr);
        //     console.log(value);
            
        //     return Math.round(Number(value)) 
        // }));
        
        this.device_ws.send(JSON.stringify(data));
    }

    /**入场记录信息回馈 */
    sendBackRecord(record_id) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'ret-report-card-record',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            record_id: record_id
        }
        this.device_ws.send(JSON.stringify(data));
    }

    /**拿到减免过的费用 */
    sendGetRealFee(min) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'time-to-money',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            card_type: "36",
            time_m: min>=0?min.toString():"0"
        }
        this.device_ws.send(JSON.stringify(data));
    }

    /**下发ID卡信息到硬件 */
    sendIDCardToDevice(opt:IDInfo) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'publish-id-card-info',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            hw_id: opt.hw_id.toString(),
            card_id: opt.card_id.toString(),
            card_type: opt.card_type.toString(),
            op_type: opt.op_type.toString(),
            time_to: opt.time_to.toString(),
        }
        this.device_ws.send(JSON.stringify(data));
    }

    /**语音播报 */
    sendRequestVoice(hw_id, money,dayOpt:dayOpt) {
        //报文格式
        let data = {
            fid: "B" + this.fid++,
            op: 'au-money',
            id_from: "001",
            id_to: "002",
            ftime: this.timeNumberToString(new Date().getTime()),
            hw_id: hw_id.toString(),
            day:dayOpt.day.toString(),
            hour:dayOpt.hour.toString(),
            min:dayOpt.min.toString(),
            money: money.toString()
        }
        this.device_ws.send(JSON.stringify(data));
    }

    /**小于10前面补0 */
    private addZero(number: number) {
        if (number < 10) {
            return `0${number}`;
        }
        else {
            return number.toString();
        }
    }

    /**时间格式化，将毫秒时间输出成字符串时间*/
    timeNumberToString(timeMS: number) {
        let time = new Date(timeMS);
        return `${time.getFullYear()}-${this.addZero(time.getMonth() + 1)}-${this.addZero(time.getDate())} ${this.addZero(time.getHours())}:${this.addZero(time.getMinutes())}:${this.addZero(time.getSeconds())}`;
    }

    /**时间格式化，将字符串时间输出成毫秒时间*/
    timeStringToNumber(timeString: string) {
        return new Date(timeString.replace(new RegExp("-", "gm"), "/")).getTime();
    }

    /**布尔值转化为字符型 */
    boolToString(bool) {
        if (bool == "true" || bool == "1") {
            return "1"
        }
        else {
            return "0"
        }
    }

    /**毫秒转化为分钟 */
    msToMin(ms: number) {
        return Math.ceil(ms / 1000 / 60);
    }
}

export interface feeRule {
    priceArray: Array<number>;
    free_time: any;
    fee_type?: any;
    day_limit: any;
    free_m_no_charge: any;
}

export interface IDInfo{
    hw_id: string,
    card_id: string,
    card_type: string,
    op_type: string,
    time_to: string,
}

export interface dayOpt{
    day: string,
    hour: string,
    min: string,
}


