import { Component, OnInit } from '@angular/core';
import { NavbuttonService } from '../../services/navbutton/navbutton.service';
import { UserService } from '../../services/user.service';
import { ModalService } from '../../services/modal/modal.service';
@Component({
  selector: 'app-main-navbar',
  templateUrl: './main-navbar.component.html',
  styleUrls: ['./main-navbar.component.scss']
})
export class MainNavbarComponent implements OnInit {
  /**操作卡 */
  cardNum;
  /**操作员 */
  operator;
  /**累积金额 */
  total;
  /**免费金额 */
  freeTotal;
  /**当前时间 */
  time = new Date();
  /**窗口状态.真为最大化，假为原始化 */
  win_state = false;
  /**no-drag */
  no_drag = false;
  constructor(private nav:NavbuttonService,private user:UserService,private modal:ModalService) { 
    this.cardNum = '80001';
    this.total = '23520';
    this.freeTotal = '80001';
    this.modal.isShowModal$.subscribe((isShow)=>{
      this.no_drag = isShow;
    });
  }

  ngOnInit() {
    //更新时间
    setInterval(()=>{
      this.time = new Date();
    },1000);
    //订阅用户信息流
    this.user.userInfo$.subscribe((info)=>{
      this.operator = info;
    });
  }

  close(){
    this.nav.app_close();
  }

  min(){
    this.nav.window_hide();
  }

  max(){
    if(this.win_state){
      this.nav.window_orignal();
    }else{
      this.nav.window_show();
    }
    this.win_state = !this.win_state;
  }


}



