import { Injectable } from '@angular/core';
import { Subject,Observable } from 'rxjs'
import { RenderService } from '../render/render.service';
import { CommonService } from '../common.service';

@Injectable()
export class CardListDBService {
    private db: any = null;
    private connection = indexedDB.open('cardList_DB', 1);
    private storeName = "cardList";
    /**出场记录信息流 */
    cardList$ = new Subject<Array<any>>();
    constructor(private render: RenderService,private common:CommonService) {
        this.connection.onupgradeneeded = function (event) {
            this.db = event.target.result;
            /**创建月卡信息表 */
            this.db.createObjectStore(this.storeName, { keyPath: 'id', autoIncrement: true });
        }.bind(this);
        //连接数据库成功后
        this.connection.onsuccess = function (event) {
            console.log("Success!");
            this.db = event.target.result;
        }.bind(this);
        //连接数据库失败后
        this.connection.onerror = function (e) {
            console.log("Error");
            console.dir(e);
        }.bind(this);
    }

    /**添加 */
    addData(data:any) {
        if (!this.db) {
            return;
        }
        console.log("addPublication OK");
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        // let count = store.count();
        // count.onsuccess = function (evt) {
        let req = store.add(data);
        req.onsuccess = (evt) =>{
            this.getList();
            console.log("add success");
        };
        req.onerror = function () {
            console.error("add error", this.error);
        };
        // };
        // count.onerror = function (evt) {
        //     console.error("add error", this.error);
        // };
    }

    /**显示所有数据 */
    getList() {
        let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
        let req = store.count();
        req.onsuccess = function (evt) {
            console.log(evt.target.result);
        };
        req.onerror = function (evt) {
            console.error("add error", this.error);
        };
        req = store.openCursor();
        let outParkList = [];
        req.onsuccess = (evt) => {
            let cursor = evt.target.result;            
            if (cursor) {
                let result = evt.target.result.value;
                let item : cardItem = {
                    adminUserId:result.adminUserId,
                    parkId:result.parkId,
                    createId:result.createId,
                    createName:result.createName,
                    createTime:result.createTime,
                    updateTime:result.updateTime,
                    updateId:result.updateId,
                    updateName:result.updateName,
                    cardID:result.cardID, //卡片ID
                    cardNo:result.cardNo, //卡片号码
                    cardType:result.cardType, // 卡片类型
                    carNumber:result.carNumber, // 车牌号码
                    ownerName:result.ownerName, // 车主姓名
                    comeTime:result.comeTime, // 入场时间
                    comePark:result.comePark, // 入场车场
                    gateNumber:result.gateNumber, // 入场道闸机号
                    comePhoto:result.comePhoto, // 入场图片
                    certificatePhoto:result.certificatePhoto, // 证件图片
                    cardBalance:result.cardBalance, // 卡上余额
                    remark: result.remark // 备注
                };
                outParkList.push(item);
                cursor.continue();
                this.cardList$.next(outParkList);
            }
            
        }

    }

    /**查询数据 */
    query(k) {
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        let req = store.get(k);
        req.onsuccess = (e)=>{
            let result = e.target.result;
            let outParkList = [];
            let outParkItemt = {
                id: result.id,
                name: result.name,
                type: result.type,
                dep: result.dep,
                license: result.license,
                balance: result.balance,
            };
            outParkList.push(outParkItemt);
            this.cardList$.next(outParkList);
        };
    }


    /**删除数据库 */
    clearDB() {
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        store.clear();
        this.getList();
    }

    /**通过控制机号获得车道名称 */
    getCardInfoByCardNo(cardNo:string):Observable<any>{
        // cardNo = cardNo.replace(/0/g, '');
        let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
        let req = store.openCursor();
        // let outParkList = [];
        return Observable.create((ob)=>{
            req.onsuccess = (evt) => {
                let cursor = evt.target.result;          
                if (cursor) {
                    let result = evt.target.result.value;
                    if(cardNo == result.cardNo){
                        ob.next(result);
                    }
                    cursor.continue();
                }
            }
            req.onerror = (evt) => {
                ob.next({});                
            }
        });
    }
}

export interface cardItem {
    // cardID: string, // 卡片ID
    // cardNo: string, // 卡片号码
    // cardType: string, // 卡片类型
    // carNumber?: string, // 车牌号码
    // ownerName?: string, // 车主姓名
    // comeTime: string, // 入场时间
    // comePark?: string, // 入场车场
    // gateNumber?: string, // 道闸机号
    // comePhoto?: string, // 入场图片
    // certificatePhoto?: string, // 证件图片
    // cardBalance?: string, // 卡上余额
    // remark?: string // 备注   
        adminUserId:string,
        parkId:string,
        createId:string,
        createName:string,
        createTime:number,
        updateTime:number,
        updateId:string,
        updateName:string,
        cardID:string, //卡片ID
        cardNo:string, //卡片号码
        cardType:string, // 卡片类型
        carNumber:string, // 车牌号码
        ownerName:string, // 车主姓名
        comeTime:number, // 入场时间
        comePark:string, // 入场车场
        gateNumber:string, // 入场道闸机号
        comePhoto:string, // 入场图片
        certificatePhoto:string, // 证件图片
        cardBalance:number, // 卡上余额
        remark: string // 备注

		
}


