import { Injectable } from '@angular/core';
import { SysConfigs } from '../constants/sysConfigs';
import { Http, Response, Headers, URLSearchParams, RequestOptions } from '@angular/http';
import { BehaviorSubject, Subject } from 'rxjs'
import { DeviceService } from '../device/device.service';

export interface outParkParams {
    cardNo?: string;
    carNumber?: string,
    cardType?: string
}

export interface InParkParams {
    cardNo?: string;
    carNumber?: string;
    cardType?: string;
}

export interface releaseParams {
    carNumber?: string;
}

export interface parkingParams {
    cardNo?: string;
    carNumber?: string;
}

export interface parkingChargeParams {
    cardNo?: string;
    carNumber?: string;
    createStartTimeMs?: number;
    createEndTimeMs?: number;
}


@Injectable()
export class ParkingService {
    /**出场纪录流 */
    outParkList$ = new Subject<any>();
    /**入场纪录流 */
    inParkList$ = new Subject<any>();
    /**无卡放行记录流 */
    releaseList$ = new Subject<any>();
    /**场内车辆流 */
    parkingList$ = new Subject<any>();
    /**车辆收费记录流 */
    carChargeList$ = new Subject<any>();
    /**入场信息回馈流 */
    inParkListBack$ = new Subject<any>();
    /**出场信息回馈流 */
    outParkListBack$ = new Subject<any>();
    /**无卡放行回馈流 */
    releaseListBack$ = new Subject<any>();
    constructor(private http: Http, private sysConfigs: SysConfigs, private device: DeviceService) { }

    /**查询出场纪录 */
    getOutParkList(page, map: outParkParams = {}) {
        let _searchParams = {
            page: page,
            pageSize: 10,
            map: map,
            sort: 'outTime',
            dir: 'desc'
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'outpark/list', _searchParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    if (resp.json().object.collection) {
                        let obj = {
                            collection: resp.json().object.collection,
                            totalSize: resp.json().object.totalSize,
                            totalPage: Math.ceil(resp.json().object.totalSize / resp.json().object.pageSize)
                        }
                        this.outParkList$.next(obj);
                    }
                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**查询入场纪录 */
    getInParkList(page, map: InParkParams = {}) {
        let _searchParams = {
            page: page,
            pageSize: 10,
            map: map,
            sort: 'comeTime',
            dir: 'desc'
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'inpark/list', _searchParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().object.collection) {
                    let obj = {
                        collection: resp.json().object.collection,
                        totalSize: resp.json().object.totalSize,
                        totalPage: Math.ceil(resp.json().object.totalSize / resp.json().object.pageSize)
                    }
                    this.inParkList$.next(obj);
                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**查询无卡放行纪录 */
    getReleaseList(page, map: releaseParams = {}) {
        let _searchParams = {
            page: page,
            pageSize: 10,
            map: map,
            sort: 'createTime',
            dir: 'desc'
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'release/list', _searchParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    if (resp.json().object.collection) {
                        let obj = {
                            collection: resp.json().object.collection,
                            totalSize: resp.json().object.totalSize,
                            totalPage: Math.ceil(resp.json().object.totalSize / resp.json().object.pageSize)
                        }
                        this.releaseList$.next(obj);
                    }
                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**查询场内车辆 */
    getParkingCarList(map: parkingParams = {}) {
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'inpark/getParkingList', map, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    if (resp.json().object) {
                        let obj = {
                            collection: resp.json().object.sort((a, b) => {
                                return this.device.timeStringToNumber(b.comeTime) - this.device.timeStringToNumber(a.comeTime);
                            }),
                            totalSize: resp.json().object.length,
                            totalPage: Math.ceil(resp.json().object.length / 10)

                        }
                        this.parkingList$.next(obj);
                    }

                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**查询车辆出场收费记录 */
    getParkingCarChargeList(page, map: parkingChargeParams = {}) {
        let _searchParams = {
            page: page,
            pageSize: 10,
            map: map,
            sort: 'outTime',
            dir: 'desc'
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'outpark/list', _searchParams, headers).subscribe((resp: Response) => {
            //成功连接服务器
            if (resp.status == 200) {
                //正确返回
                if (resp.json().status == 0) {
                    if (resp.json().object.collection) {
                        let obj = {
                            collection: resp.json().object.collection,
                            totalSize: resp.json().object.totalSize,
                            totalPage: Math.ceil(resp.json().object.totalSize / resp.json().object.pageSize)
                        }
                        this.carChargeList$.next(obj);
                    }
                }
                //错误返回
                else {
                    alert(resp.json().message);
                }
            }
            //状态码为400+
            else if (resp.status >= 400 && resp.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (resp.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**新增入场纪录 */
    addInParkList(data) {
        let parameter = {
            list: data
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'inpark/synch', parameter, headers).subscribe((resp) => {
            //正确返回
            if (resp.json().status == 0) {
                let back ={
                    isConnect:true,
                    data:resp.json().object
                }
                this.inParkListBack$.next(back);
            }
            //错误返回
            else {
                alert(resp.json().message);
            }
        }, (error) => {
            let back ={
                isConnect:false,
                data:data
            }
            this.inParkListBack$.next(back);
            if (error.status >= 400 && error.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (error.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**新增出场纪录 */
    addOutParkList(data) {
        let parameter = {
            list: data
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'outpark/synch', parameter, headers).subscribe((resp: Response) => {
            //正确返回
            if (resp.json().status == 0) {
                let back ={
                    isConnect:true,
                    data:resp.json().object
                }
                this.outParkListBack$.next(back);
            }
            //错误返回
            else {
                alert(resp.json().message);
            }
        }, (error) => {
            let back ={
                isConnect:false,
                data:data
            }
            this.outParkListBack$.next(back);
            if (error.status >= 400 && error.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (error.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }

    /**新增无卡放行纪录 */
    addReleaseList(data) {
        let parameter = {
            list: data
        }
        let headers = new RequestOptions({ headers: new Headers({ 'Content-Type': this.sysConfigs.CONTENT_TYPE_JSON, 'X-Access-Token': localStorage.getItem('token') }) });
        this.http.post(this.sysConfigs.SERVER_PARKING + 'release/synch', parameter, headers).subscribe((resp: Response) => {
            //正确返回
            if (resp.json().status == 0) {
                let back ={
                    isConnect:true,
                    data:resp.json().object
                }
                this.releaseListBack$.next(back);
            }
            //错误返回
            else {
                alert(resp.json().message);
            }
        }, (error) => {
            let back ={
                isConnect:false,
                data:data
            }
            this.releaseListBack$.next(back);
            if (error.status >= 400 && error.status < 500) {
                alert('没有此服务');
            }
            //状态码为500+
            else if (error.status >= 500) {
                alert('与服务器连接失败');
            }
        });
    }



}


