import { Component, OnInit } from '@angular/core';
import { ParkLotService, } from '../../services/parkLot/parkLot.service';

@Component({
  selector: 'app-main-parkinfo',
  templateUrl: './main-parkinfo.component.html',
  styleUrls: ['./main-parkinfo.component.scss']
})
export class MainParkinfoComponent implements OnInit {
  parkInfoObj = {
    monthCardNum: 0,
    temporaryCard: 0,
    inWithHand: 0,
    outWithHand: 0,
    remaining: 0,
    total: 0
  };
  constructor(private parkInfo:ParkLotService) {
    // console.log('hello');
    this.parkInfo.parkLotObj$.subscribe((info)=>{
      this.parkInfoObj = info;
    })
    this.parkInfo.setTotal(360);
    this.parkInfo.setRemaining(60);    
   }

  ngOnInit() {
  }

}



