import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { RenderService } from '../render/render.service';

@Injectable()
export class InparkdbService {
    connection = indexedDB.open('inParkList_DB', 1);
    private db: any = null;
    private storeName = "inPark";
    /**出场记录信息流 */
    inParkList$ = new Subject<Array<inParkItem>>();
    constructor(private render: RenderService) {
        this.connection.onupgradeneeded = function (event) {
            this.db = event.target.result;
            this.store = this.db.createObjectStore(this.storeName, { keyPath: 'id', autoIncrement: true });

        }.bind(this);
        //连接数据库成功后
        this.connection.onsuccess = function (event) {
            console.log("Success!");
            this.db = event.target.result;
        }.bind(this);
        //连接数据库失败后
        this.connection.onerror = function (e) {
            // console.log("Error" + e.target.errorCode);
            console.dir(e);
            console.dir(this.connection);
            console.dir(this.db);
        }.bind(this);

    }


    /**添加 */
    addData(data: inParkItem) {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        let req = store.add(data);
        req.onsuccess = (evt) => {
            this.getList();
        };
        req.onerror = function () {
            console.error("add error", this.error);
        };
    }

    /**批量添加数据 */
    addDatas(datas: Array<inParkItem>) {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        datas.forEach((data) => {
            let req = store.add(data);
            req.onsuccess = (evt) => {
                this.getList();
            };
            req.onerror = function () {
                console.error("add error", this.error);
            };
        });
    }

    /**显示所有数据 */
    getList() {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
        /**数据库长度 */
        let len;
        let req = store.count();
        req.onsuccess = (evt) => {
            len = evt.target.result;
            let inParkList = [];
            this.inParkList$.next(inParkList);
            if (len == 0) {
                this.inParkList$.next(inParkList);
            }
            else {
                let request = store.openCursor();
                request.onsuccess = (evt) => {
                    let cursor = evt.target.result;
                    if (cursor) {
                        let result = cursor.value;
                        let item: inParkItem = {
                            adminUserId: result.adminUserId,
                            parkId: result.parkId,
                            createId: result.createId,
                            createName: result.createName,
                            createTime: result.createTime,
                            updateTime: result.updateTime,
                            updateId: result.updateId,
                            updateName: result.updateName,
                            cardID: result.cardID, // 卡片ID
                            cardNo: result.cardNo, // 卡片号码
                            cardType: result.cardType, // 卡片类型
                            carNumber: result.carNumber, // 车牌号码
                            ownerName: result.ownerName, // 车主姓名
                            comeTime: result.comeTime, // 入场时间
                            comePark: result.comePark, // 入场车场
                            gateNumber: result.gateNumber, // 道闸机号
                            comePhoto: result.comePhoto, // 入场图片
                            certificatePhoto: result.certificatePhoto, // 证件图片
                            cardBalance: result.cardBalance, // 卡上余额
                            remark: result.remark, // 备注
                            recordId: result.recordId
                        };
                        inParkList.push(item);
                        cursor.continue();
                        if (inParkList.length == len) {
                            this.inParkList$.next(inParkList);
                            this.exportData(inParkList);
                        }
                    }
                    request.onerror = function (evt) {
                        console.error("add error", this.error);
                    };
                }
            }
        }
        req.onerror = function (evt) {
            console.error("add error", this.error);
        };
    }

    /**查询数据 */
    query(record_id) {
        if (!this.db) {
            return;
        }
        return Observable.create((ob) => {
            let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
            let len;
            let req = store.count();
            req.onsuccess = (evt) => {
                len = evt.target.result;
                let isExist = false;
                if (len == 0) {
                    ob.next(isExist);
                    console.log(len);
                    
                }
                else {
                    let request = store.openCursor();
                    let querynumber = 0;
                    request.onsuccess = (evt) => {
                        let cursor = evt.target.result;
                        if (cursor) {
                            let result = evt.target.result.value;
                            if (record_id == result.recordId && isExist == false) {
                                isExist = true;
                                ob.next(isExist);                               
                                querynumber += 1;
                                console.log(len);
                                
                            }
                            else if (record_id != result.recordId || isExist == true) {
                                querynumber += 1;
                            }
                            if (querynumber == len && isExist == false) {
                                ob.next(isExist);                                
                                console.log(len);
                                
                            }
                            cursor.continue();
                        }
                    }
                    request.onerror = (evt) => {
                        ob.next(isExist);      
                        console.log(len);
                                         
                    }
                }
            }
            req.onerror = function (evt) {
                console.error("add error", this.error);
            };
        });
    }

    /**删除数组里面的数据 */
    deleteDatas(record_id) {
            let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
            let len;
            let req = store.count();
            req.onsuccess = (evt) => {
                len = evt.target.result;
                let isExist = false;
                if (len == 0) {
                    return;
                }
                else {
                    let request = store.openCursor();
                    let querynumber = 0;
                    request.onsuccess = (evt) => {
                        let cursor = evt.target.result;
                        if (cursor) {
                            let result = evt.target.result.value;
                            if (record_id == result.recordId && isExist == false) {
                                let dereq = cursor.delete();
                                querynumber += 1;                                                                    
                                dereq.onsuccess = () => {
                                };
                                dereq.onerror = () => {
                                    console.log("删除失败");
                                };
                            }
                            else if (record_id != result.recordId || isExist == true) {
                                querynumber += 1;
                            }
                            if (querynumber == len) {                             
                            }
                            cursor.continue();
                        }
                    }
                    request.onerror = (evt) => {
                    }
                }
            }
            req.onerror = function (evt) {
                console.error("add error", this.error);
            };
    }

    /**删除数据库 */
    clearDB() {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        store.clear();
        this.getList();
    }

    /**导出数据 */
    exportData(datas) {
        let data = {
            list: datas
        }
        this.render.send('save-db-inpark', JSON.stringify(data));
    }
}

export interface inParkItem {
    adminUserId: string;
    parkId: string;
    createId: string;
    createName: string;
    createTime: any;
    updateTime: any;
    updateId: string;
    updateName: string;
    recordId?: any,
    cardID?: string; // 卡片ID
    cardNo: string; // 卡片号码
    cardType: string; // 卡片类型
    carNumber?: string; // 车牌号码
    ownerName?: string; // 车主姓名
    comeTime: number; // 入场时间
    comePark?: string; // 入场车场
    gateNumber?: string; // 道闸机号
    comePhoto?: string; // 入场图片
    certificatePhoto?: string; // 证件图片
    cardBalance?: number; // 卡上余额
    remark?: string; // 备注
}


