import { Component, OnInit } from '@angular/core';
import { PrivilegeService } from '../../services/privilege/privilege.service';
import { PrivilegedbService, privilegeItem } from '../../services/DB/privilegeListDB';

@Component({
  selector: 'app-menu-updatePrivilege',
  templateUrl: './menu-updatePrivilege.component.html',
  styleUrls: ['./menu-updatePrivilege.component.scss']
})
export class MenuUpdatePrivilegeComponent implements OnInit {
  head = ['序号', '票名', '减免时间(分钟)','单次使用上限','可叠加类型'];
  privilegeList = [];
  // privilegeListDB = [];
  constructor(private privilege: PrivilegeService, private DB: PrivilegedbService) {
    //当数据更新时下载到数据库
    this.privilege.privilegeList$.filter(list =>{return list.length>0}).subscribe((list) => {
      // this.privilegeList = list;
      if (list[0].parkId == JSON.parse(localStorage.adminUser).parkId) {
        this.DB.clearDB();        
        this.DB.addDatas(list);
      }
      else {
        alert("账户错误");
      }
    });
    //显示数据库里面的信息
    this.DB.privilegeList$.subscribe((list) => {
      this.privilegeList = list;
    });
  }

  ngOnInit() {
    this.privilege.getPrivilegeList();
    this.DB.getList();    
  }
  addData() {
    this.privilege.getPrivilegeList();
  }
}



