import { Component, OnInit, NgModule } from '@angular/core';
import { ControllerService } from '../../services/controller/controller.service';
import { DeviceService, feeRule } from '../../services/device/device.service';
import { UserService } from '../../services/user.service';

interface bodyItem {
  parkHour: number;
  parkMoney: any;
}

declare let $: any;
@Component({
  selector: 'app-menu-setController',
  templateUrl: './menu-setController.component.html',
  styleUrls: ['./menu-setController.component.scss']
})
export class MenuSetControllerComponent implements OnInit {
  controllerSelect: boolean = false;
  /*让控制机时间流动*/
  /*控制器时间定时器*/
  controllerTimer: any;
  /*选择控制机数组*/
  controllerSelectArray: Array<any> = [];
  controllers: Array<any> = [];
  /*控制机时间*/
  controllerTimeparamsArray: Array<any> = [];
  controllertimer: string;
  //tab转化
  time_panel = {
    isShow: false
  };
  standard_panel = {
    isShow: false
  };
  clear_panel = {
    isShow: false
  };
  display_panel = {
    isShow: false
  };
  deviceTime = new Date().getTime();
  systemTime = new Date().getTime();
  /*小时数据数组*/
  body = [];
  /*收费标准标题数据*/
  chargeTitle: string = '';
  /*下发控制器参数*/
  controllerParams: feeRule;
  /**收费页面 */
  chargeObj = {
    //卡类
    cardType: "36",
    //收费限额
    maxDayFee: "",
    //免费分钟
    freeMinutes: "",
    //免费分钟是否计费
    chargeInFreeMinu: 0
  }

  constructor(public controllerService: ControllerService, public deviceService: DeviceService, private user: UserService) {
    this.body.length = 24;
    /*获取控制机信息*/
    this.deviceService.onmessage$.filter(data => data.ret == "0").filter(data => data.op === "ret-hb").subscribe((data) => {
      let checkState: any = this.controllers;
      this.controllers = data.hw;
      for (let i = 0; i < this.controllers.length; i++) {
        checkState.forEach((element) => {
          if (element.hw_id == this.controllers[i].hw_id) {
            this.controllers[i]["controllerSelect"] = element.controllerSelect ? element.controllerSelect : false;
          }
        });
        this.controllerTimeparamsArray.push(data.hw_id);
      }
    });
    /*获取控制机时间*/
    this.deviceService.onmessage$.filter(data => data.ret == "0").filter(data => data.op === "ret-get-hw-time").subscribe((data) => {
      this.controllertimer = data.ftime;
      let controlertimerSecond: number = this.deviceService.timeStringToNumber(this.controllertimer);
      this.controllerTimer = window.setInterval(() => {
        /*这里的1000代表一秒*/
        controlertimerSecond = Number(controlertimerSecond + 1000);
        this.controllertimer = this.deviceService.timeNumberToString(controlertimerSecond);
      }, 1000);
      // let myNum=Number(22);
    });
    /*得到控制机收费标准数据*/
    this.deviceService.onmessage$.filter(data => data.ret == "0").filter(data => data.op === "ret-price-get").subscribe((data) => {
      if (data.price != null) {
        this.body = data.price;
        this.chargeObj = {
          cardType: "36",
          maxDayFee: data.day_limit,
          freeMinutes: data.free_m,
          chargeInFreeMinu: Number(data.free_m_no_charge)
        }
      }
    });
    /*下发到控制机*/
    this.deviceService.onmessage$.filter(data => data.ret == "0").filter(data => data.op === "ret-price-set").subscribe((data) => {
      console.log("下发数据是");
      console.log(JSON.stringify(data));
    });
    /**订阅云端的数据 */
    this.controllerService.controller$.subscribe((data) => {
      this.chargeObj = {
        cardType: "36",
        maxDayFee: data.maxDayFee,
        freeMinutes: data.freeMinutes,
        chargeInFreeMinu: data.chargeInFreeMinu
      }
      if (data.feestandardItems && data.feestandardItems.length > 0) {
        this.chargeTitle = "云端";
        data.feestandardItems.forEach((element) => {
          /*先新建一个数组，将得到的数据数组中的对象的键和值赋值给新建数组的键*/
          this.body[element.parkHour - 1] = element.parkMoney;
        });
      }
      localStorage.feeStandard = JSON.stringify(this.chargeObj);
    });
    /*通过sendHb方法得到订阅的数据*/
    this.deviceService.sendHb();


  }
  ngOnInit() {
    this.time_panel.isShow = true;
    /*请求控制机数据*/
    // this.controllerService.sendRequestToGetTime("1");
    this.user.isLogin = false;
    /*更新当前系统时间*/
    setInterval(() => {
      this.systemTime = new Date().getTime();
    }, 1000);
  }
  /*加载时间选项卡*/
  toControllerSelect(ctrlObject) {
    if (ctrlObject.controllerSelect) {
      this.controllerSelectArray.push(ctrlObject);
    }
    else {
      if (this.controllerSelectArray.length > 0) {
        for (let i = 0; i < this.controllerSelectArray.length; i++) {
          if (this.controllerSelectArray[i] == ctrlObject) {
            this.controllerSelectArray.splice(i, 1);

          }
        }
      }
    }
    this.controllerSelectArray.sort(this.sortType);


  }
  /*数组根据type进行排序排序*/
  sortType(a, b) {
    return parseInt(a.type) - parseInt(b.type);
  }
  toReadControllerTime() {
    if (this.controllerSelectArray.length > 0) {
      this.deviceService.sendGetTime(this.controllerSelectArray[0].hw_id);
      /*请求完数据，清除定时器和清空值*/
      if (this.controllerTimer != null) {
        window.clearInterval(this.controllerTimer);
        this.controllertimer = null;
      }
    }

  }
  /*加载时间*/
  loadTime() {

  }
  /*点击切换选项卡*/
  changeTabs(labelName) {
    this.closeAllTabs();
    switch (labelName) {
      case 'time_panel':
        this.time_panel.isShow = true;
        return;
      case 'standard_panel':
        this.standard_panel.isShow = true;
        return;
      case 'clear_panel':
        this.clear_panel.isShow = true;
        return;
      case 'display_panel':
        this.display_panel.isShow = true;
        return;
      default:
        this.time_panel.isShow = true;
        return;
    }

  }
  closeAllTabs() {
    this.time_panel.isShow = false;
    this.standard_panel.isShow = false;
    this.clear_panel.isShow = false;
    this.display_panel.isShow = false;
  }
  selectControllerNumber(i) {
    console.log("i=" + i);
    /*<li *ngFor="let controller of controllers; let i=index" (click)="selectControllerNumber(i)">{{controller.name}}</li> */
    this.controllers[i].isShow = true;
  }
  /*获取云端数据*/
  getDataFromCloud() {
    /*请求服务数据*/
    this.controllerService.getControllerData();
  }
  readControllerCharge() {
    /*用户没勾选项控制机*/
    if (this.controllerSelectArray.length > 0) {
      console.log("控制机");
      this.chargeTitle = "控制机";
      this.deviceService.sendGetFeeRules(this.controllerSelectArray[0].hw_id);
    }
    else {
      alert("请勾选控制机");
    }
  }
  sendDataToController() {
    if (this.controllerSelectArray.length > 0) {
      let controllerParams: feeRule = {
        priceArray: this.body,
        free_time: this.chargeObj.freeMinutes,
        day_limit: this.chargeObj.maxDayFee,
        free_m_no_charge: String(this.chargeObj.chargeInFreeMinu),
      }
      localStorage.feeStandard = JSON.stringify({
        cardType: "36",
        maxDayFee: Number(this.chargeObj.maxDayFee),
        freeMinutes: Number(this.chargeObj.freeMinutes),
        chargeInFreeMinu: String(this.chargeObj.chargeInFreeMinu)
      });

      for (let i = 0; i < this.controllerSelectArray.length; i++) {
        this.deviceService.sendSetFeeRules(this.controllerSelectArray[i].hw_id, controllerParams);
      }
    }
    else {
      alert("请勾选控制机");
    }
  }
  trackByIndex(index: number, value: number) {
    // return index;
  }
}




