import { Component, OnInit } from '@angular/core';
import { DeviceService } from '../../services/device/device.service';
import { UserService } from '../../services/user.service';

@Component({
  selector: 'app-menu-setBaseInfo',
  templateUrl: './menu-setBaseInfo.component.html',
  styleUrls: ['./menu-setBaseInfo.component.scss']
})
export class MenuSetBaseInfoComponent implements OnInit {
  info:baseinfo = {
    computerName: "",
    macAddress: "",
    passWord: ""
  }
  constructor(private device: DeviceService,private user:UserService) {
    /*获取控制机信息*/
    this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op == "ret-hb").subscribe((data) => {
      this.info.macAddress = data.id_mac;
    });
    // this.user.baseInfo$.subscribe((data)=>{
    //   this.info.PCName = JSON.parse(localStorage.baseInfo).PCName;
    //   this.info.password = JSON.parse(localStorage.baseInfo).password;
    // });
    /**读取本地信息*/
    this.getLocalInfo();
  }

  ngOnInit() {
    
  }

  getLocalInfo(){
    if(localStorage.baseInfo){
      this.info.computerName = JSON.parse(localStorage.baseInfo).PCName;
      this.info.passWord = JSON.parse(localStorage.baseInfo).password;
    }
  }

  //同步数据
  updateBaseInfo(){
    localStorage.baseInfo = JSON.stringify(this.info);
    this.user.getBaseInfo(this.info);
  }

}

interface baseinfo{
  passWord:string;
  computerName:string;
  macAddress:string;
}



