import { Component, OnInit } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs'
import { CardManageService } from '../../services/cardManage/cardManage.service';
import { CardListDBService } from '../../services/DB/cardListDB';
@Component({
  selector: 'app-menu-cardDownload',
  templateUrl: './menu-cardDownload.component.html',
  styleUrls: ['./menu-cardDownload.component.scss']
})
export class MenuCardDownloadComponent implements OnInit {
  proportion;
  download;
  total;
  i = 1;
  currentPage = 1;  
  constructor(private card: CardManageService, private cardDB: CardListDBService) {
    // let a = setInterval(() => {
    //   if (this.download < this.total) {
    //     ++this.download;
    //     this.proportion = (this.download / this.total * 100) + "%";
    //   }
    //   else {
    //     clearInterval(a);
    //   }
    // }, 100);

    this.card.cardInfoDownLoad$.subscribe((data: any) => {
      let body = data.collection;
      this.total = data.totalSize;
      let totalPage = data.totalPage;
      if (this.currentPage <= totalPage) {     
        this.currentPage += 1;
        this.card.getCardInfo(this.currentPage);
        body.forEach(element => {
          this.i;
          this.cardDB.addData(element);
          this.download = this.i++;
          this.transPer(this.download, this.total);
        });
      }

    });
  }

  ngOnInit() {

  }

  /**转化百分比 */
  transPer(download, total) {
    this.proportion = (this.download / this.total * 100) + "%";
  }

  /**查找 */
  downLoad() {
    this.i = 1;
    this.currentPage = 1;  
    this.cardDB.clearDB();        
    this.card.getCardInfo(1);
  }

}



