import { Component, OnInit } from '@angular/core';
import { ParkingService } from '../../services/parking/parking.service';
import { OutparkdbService,outParkItem } from '../../services/DB/outparkListDB';

@Component({
  selector: 'app-menu-updateOutParking',
  templateUrl: './menu-updateOutParking.component.html',
  styleUrls: ['./menu-updateOutParking.component.scss']
})
export class MenuUpdateOutParkingComponent implements OnInit {
  body;
  head = ['序号', '卡号', '车牌号码', '卡类', '入场车道', '出场车道', '入场时间', '停车时间','出场时间','应收', '实收', '优惠原因'];

  constructor(private outpark: OutparkdbService,private parking: ParkingService) { 
    this.outpark.outParkList$.subscribe((outlist)=>{
      this.body = outlist;
    });
  }

  ngOnInit() {
    this.outpark.getList();
  }
  addData(){
    let adminUser = JSON.parse(localStorage.adminUser);
    let comm = new Date().getTime();

    let data:outParkItem = {      
      adminUserId:adminUser.adminUserId,
      parkId:adminUser.parkId,
      createId:adminUser.createId,
      createName:adminUser.createName,
      createTime:adminUser.createTime, //+adminUser.createTime,
      updateTime:adminUser.updateTime, //+adminUser.updateTime,
      updateId:adminUser.updateId,
      updateName:adminUser.updateName,
      cardID:"1001", //卡片ID
      cardNo:"2002", //卡片号码
      cardType:"月卡", // 卡片类型
      carNumber:"100010", // 车牌号码
      ownerName:"hello", // 车主姓名
      comeTime:comm, // 入场时间
      comePark:"1", // 入场车场
      gateNumber:"11s", // 入场道闸机号
      comePhoto:"result.comePhoto", // 入场图片
      certificatePhoto:"result.certificatePhoto", // 证件图片
      cardBalance:42355, // 卡上余额
      outTime:comm, // 出场时间
      outPark:"result.outPark", // 出场车场
      outGateNumber:"result.outGateNumber", // 出场道闸机号
      outPhoto:"result.outPhoto", // 出场图片
      feeMoney:2234, // 收费金额
      amountReceive:42355.32, // 应收金额
      timeoutFlag:1, // 超时标志
      timeoutHour:comm, // 超时收费时间
      timeoutMoney:42355, // 超时收费金额
      freeReason:"result.freeReason", // 免费原因
      freeReasonId:"result.freeReasonId", // 免费原因Id
      remark: "result.remark", // 备注
      recordId:"100110"
    }

    this.outpark.addData(data);

  }

  sendData(){
    this.parking.addOutParkList(this.body);
  }

  clear(){
    this.outpark.clearDB();
  }



}



